import Pagination from "../Pagination/PaginationComponent";
import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";
import Moment from "moment";
import PaginationComponent from "../Pagination/PaginationComponent";
export default function DatatableUserList({ data }) {
  /*TOGGLE INSTITUTE STATUS START*/
  const [isActive, setisActive] = useState(false);
  let status;
  function toggleInstStatus(id) {
    setisActive(isActive ? false : true);
    isActive ? (status = 1) : (status = 0);
    // console.log("STATUS: " + id);
    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-status-update",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          active: status,
        },
      };
      const instStatusApiResp = await trackPromise(axios(config));
      //console.log("instStatusApiResp - " + instStatusApiResp.data);
      if (instStatusApiResp.data.status === "ok") {
        toast(`Institute Status ${status ? "Active" : "Inactive"}`, {
          className: "toast-error",
        });
      } else {
        toast(instStatusApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  /*TOGGLE INSTITUTE STATUS END*/

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">First Name</th>
            <th className="inst-list-th text-center">Last Name</th>
            <th className="inst-list-th text-center">Email</th>
            <th className="inst-list-th text-center">User Type</th>
            <th className="inst-list-th text-center" colSpan="3">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              {/* <td className="inst-list-td text-center">
                                      {row["id"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["id"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["name"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["email"]}
                                    </td>
                                    <td className="inst-list-td text-center">
                                      {row["body"]}
                                    </td> */}
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center">{row["fname"]}</td>
              <td className="inst-list-td text-center">{row["lname"]}</td>
              <td className="inst-list-td text-center">{row["email"]}</td>
              <td className="inst-list-td text-center">{row["role"]}</td>
              <td
                className="inst-list-td text-center action"
                title="View Detail"
              >
                <Link
                  to={{
                    pathname: "/user-detail-view",
                    state: { email: row["email"] },
                  }}
                >
                  <i className="far fa-eye action-icon"></i>
                </Link>
              </td>
              <td className="inst-list-td text-center action" title="Edit">
                <Link
                  to={{
                    pathname: "/update-user",
                    state: { email: row["email"] },
                  }}
                >
                  <i className="far fa-edit action-icon"></i>
                </Link>
              </td>
              <td className="inst-list-td text-center action" title="Status">
                <i className="fas fa-toggle-on action-icon"></i>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
