import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
export default function BillingInvoiceView() {
  const [data, setData] = useState([]);
  const [q, setQ] = useState("");

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/institutes",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const allPkgApiResp = await trackPromise(axios(config));
      // console.log("allPkgApiResp - " + allPkgApiResp.data.msg.data);
      setData(allPkgApiResp.data.msg.data);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  //search
  function search(rows) {
    const columns = rows[0] && Object.keys(rows[0]);

    return rows.filter((row) =>
      columns.some(
        (column) =>
          row[column].toString().toLowerCase().indexOf(q.toLowerCase()) > -1
      )
    );
  }

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Invoice
                      </h4>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* <div className="d-flex float-left mb-3">
                            <Link
                              to="/payment-history-view"
                              className="border ml-1 inst-list-search-div"
                              title="Payment History"
                            >
                              <i className="serach-icon-inst-list fas fa-history"></i>
                            </Link>
                          </div> */}
                          <table className="table table-sm table-borderless table-responsive">
                            <tbody className="p-0 m-0">
                              <tr>
                                <td className="pl-0">Invoice ID</td>
                                <td className="pl-0 pr-0">ASDF546SDF</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Institute ID</td>
                                <td className="pl-0 pr-0">UNIV-BRAC-102</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Institute Name</td>
                                <td className="pl-0 pr-0">Brac University</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Billing Month</td>
                                <td className="pl-0 pr-0">January</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Billing Year</td>
                                <td className="pl-0 pr-0">2020</td>
                              </tr>
                              <tr className="bottom-border">
                                <td className="pl-0">Billing Amount</td>
                                <td className="pl-0 pr-0">4500 BDT</td>
                              </tr>
                            </tbody>
                          </table>
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
