import React, { useState, useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import { ProfileContext } from "../../ProfileContext";

const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function UploadAvatar() {
  const [isProfileUpdate, setIsProfileUpdate] = useContext(ProfileContext);
  const [profileImg, setProfileImg] = useState("./img/avatar-default.png");

  //GET PROFILE PICTURE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/user-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      let userApiResp = await trackPromise(axios(config));
      userApiResp = userApiResp.data.msg;
      // console.log("userApiResp - " + JSON.stringify(userApiResp.avatar_img));
      setProfileImg(userApiResp.avatar_img);
    }
    fetchData();
  }, []);
  //GET PROFILE PICTURE  -END

  const imageHandlerAvatar = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        console.log(
          "imageHandlerAvatar calling...." + JSON.stringify(reader.result)
        );
        setProfileImg(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };

  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  const onSubmitAvatar = (data) => {
    //console.log(data.avatar_img[0]);

    const formData = new FormData();
    formData.append("avatar_img", data.avatar_img[0]);

    var config = {
      method: "post",
      url: "/avatar-update?",
      data: formData,
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast("Profile Picture Uploaded Successfully", {
            className: "toast-error",
          });
          setIsProfileUpdate(true);
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };
  return (
    <>
      {/* Upload profile image start */}
      <form onSubmit={handleSubmit(onSubmitAvatar)}>
        <div className="form-group row" style={{ margin: "24px" }}>
          <div className="col">
            <div style={{ marginTop: "16px", textAlign: "center" }}>
              <div className="img-holder-avatar">
                <img src={profileImg} id="img" className="imgAvatar" />

                <input
                  type="file"
                  name="avatar_img"
                  accept="image/*"
                  id="inputAvatar"
                  onChange={imageHandlerAvatar}
                  ref={register({
                    required: "* Please Select a File",
                    validate: (value) => {
                      if (value[0].size > 1000000) {
                        return "Photo size exceeds 1 MB";
                      }
                      return true;
                    },
                  })}
                />
                <label
                  className="image-upload-avatar"
                  htmlFor="inputAvatar"
                  title="Upload Profile Picture"
                >
                  <i className="fas fa-camera"></i>
                </label>
              </div>
              <span className="small">
                Upload Profile Photo (Max: 1 MB | File Type: jpg, png, jpeg,
                gif)
              </span>
              <br />
              {errors.avatar_img && (
                <ErrorMsg>{errors.avatar_img.message}</ErrorMsg>
              )}
              <button type="submit" className="btn btn-update mt-3">
                <i className="far fa-caret-square-up mr-3"></i>
                Update
              </button>
            </div>
          </div>
        </div>
      </form>
      {/* Upload profile image end */}
      <LoadingIndicator />
    </>
  );
}
