import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function CreatePackage() {
  const { register, handleSubmit, errors } = useForm();
  let history = useHistory();
  // POST PACKAGE DATA -START
  const onSubmit = (data) => {
    //seperate package vlaues from data object start
    const packageData = {
      package: {
        title: data.packageTitle,
        description: data.packageDescription,
        display: data.display,
        type: "default",
      },
      parameters: pkgParamList,
    };
    // console.log("Package Data: " + JSON.stringify(packageData));
    //seperate package vlaues from data object end
    postData(packageData);
    async function postData(packageData) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-save",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: packageData,
      };
      // console.log("PACKAGE DATA---" + JSON.stringify(packageData));
      const savePkgApiResp = await trackPromise(axios(config));
      //console.log("savePkgApiResp - " + savePkgApiResp.data);
      if (savePkgApiResp.data.status === "ok") {
        toast("Package Created Successfully", {
          className: "toast-error",
        });
        history.push("/package-list-view");
      } else {
        toast(savePkgApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  };
  // POST PACKAGE DATA -END

  /* PACKAGE PARAMETER START*/
  const [pkgParamList, setPkgParamList] = useState([
    { name: "", price: "", quantity: "" },
  ]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...pkgParamList];
    list[index][name] = value;
    setPkgParamList(list);
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...pkgParamList];
    list.splice(index, 1);
    setPkgParamList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setPkgParamList([...pkgParamList, { name: "", price: "", quantity: "" }]);
  };

  /* PACKAGE PARAMETER END*/
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Create a Package
                      </h4>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* create package form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>
                                  Package Name
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="packageTitle"
                                  ref={register({ required: true })}
                                />
                                {errors.packageTitle && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>
                                  Package Description
                                </label>
                                <br />
                                <textarea
                                  className="form-control"
                                  name="packageDescription"
                                  ref={register({ required: true })}
                                  rows="1"
                                  cols="30"
                                ></textarea>
                                {errors.packageDescription && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>
                                  Is Package Display on Front-end ?
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      type="radio"
                                      className="mr-2"
                                      value="On"
                                      ref={register({ required: true })}
                                    />
                                    On
                                  </div>
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      className="mr-2"
                                      type="radio"
                                      value="Off"
                                      ref={register({ required: true })}
                                    />
                                    Off
                                  </div>
                                </div>

                                {errors.display && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>

                            <div className="form-group">
                              <label>
                                <span className="red-star">*</span> Package
                                Parameter
                                <span
                                  onClick={handleAddClick}
                                  title="Add New Parameter"
                                  className="shadow round-plus float-right badge badge-bg mb-3"
                                  style={{ padding: "10px 24px" }}
                                >
                                  <i className="fas fa-plus"></i>
                                </span>
                              </label>
                              <br />
                              {pkgParamList.map((x, i) => {
                                return (
                                  <div className="row" key={i}>
                                    <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                      <input
                                        type="text"
                                        className="form-control mb-3"
                                        name="name"
                                        value={x.name}
                                        onChange={(e) =>
                                          handleInputChange(e, i)
                                        }
                                        placeholder="Title"
                                        required
                                        ref={register({ required: true })}
                                      />
                                      {errors.name && (
                                        <ErrorMsg>
                                          * This Field is Required
                                        </ErrorMsg>
                                      )}
                                    </div>

                                    <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                      <input
                                        type="number"
                                        className="form-control mb-3"
                                        name="price"
                                        value={x.price}
                                        min="0"
                                        required
                                        onChange={(e) =>
                                          handleInputChange(e, i)
                                        }
                                        placeholder="Price"
                                        ref={register({ required: true })}
                                      />
                                      {errors.price && (
                                        <ErrorMsg>
                                          * This Field is Required
                                        </ErrorMsg>
                                      )}
                                    </div>
                                    <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                      <input
                                        type="number"
                                        className="form-control mb-3"
                                        name="quantity"
                                        value={x.quantity}
                                        min="0"
                                        required
                                        onChange={(e) =>
                                          handleInputChange(e, i)
                                        }
                                        placeholder="Quantity"
                                        ref={register({ required: true })}
                                      />
                                      {errors.quantity && (
                                        <ErrorMsg>
                                          * This Field is Required
                                        </ErrorMsg>
                                      )}
                                    </div>
                                    <div className="col-sm-12 col-md-2 col-lg-2 col-xl-2">
                                      {i === 0 ? null : (
                                        <span
                                          onClick={() => handleRemoveClick(i)}
                                          title="Delete"
                                          className="shadow round-minus badge badge-bg mb-3"
                                          style={{ padding: "10px 24px" }}
                                        >
                                          <i className="fas fa-minus"></i>
                                        </span>
                                      )}
                                    </div>
                                  </div>
                                );
                              })}
                            </div>

                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Create
                            </button>
                          </form>
                          {/* create package form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
