import React from "react";
import Footer from "../Layout/Footer";
export default function Dashboard() {
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row mt-5">
                {/* left side content */}
                <div className="col-sm-12 col-md-12 col-lg-8">
                  {/* infobox start */}
                  <div className="row">
                    {/* total teacher */}
                    <div className="col-sm-12 col-md-4 col-lg-4">
                      <div className="thumbnail">
                        <img
                          src="img/total_teacher.png"
                          className="img-fluid"
                          alt="total teacher"
                        />
                        <div className="text-over-img d-flex flex-column">
                          <div className="info1">Total Teacher</div>
                          <div className="info2">27</div>
                          <div className="info3">Active 23</div>
                        </div>
                      </div>
                    </div>
                    {/* total student */}
                    <div className="col-sm-12 col-md-4 col-lg-4">
                      <div className="thumbnail">
                        <img
                          src="img/total_student.png"
                          className="img-fluid"
                        />
                        <div className="text-over-img d-flex flex-column">
                          <div className="info1">Total Student</div>
                          <div className="info2">273</div>
                          <div className="info3">Enrolled 223</div>
                        </div>
                      </div>
                    </div>
                    {/* total course */}
                    <div className="col-sm-12 col-md-4 col-lg-4">
                      <div className="thumbnail">
                        <img src="img/total_course.png" className="img-fluid" />
                        <div className="text-over-img d-flex flex-column">
                          <div className="info1">Total Course</div>
                          <div className="info2">27</div>
                          <div className="info3">Available 13</div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {/* infobox end */}
                  {/* attendance full div start */}
                  <div className="row p-2 mt-3">
                    <div className="col">
                      <div className="info-box">
                        <div className="info-box-content attendance-thumbnail">
                          <img
                            src="img/attendance.png"
                            className="attendance-img mr-5"
                          />
                          <span className="info-box-text attendance-text">
                            Attendance
                          </span>
                          <div className="row">
                            {/* attendance left part */}
                            <div className="col-sm-12 col-md-8 col-lg-8 col-xl-8">
                              {/* select input part */}
                              <div className="row p-2 mt-3">
                                <div className="col-sm-12 col-md-4 col-lg-4">
                                  {/* select */}
                                  <div className="form-group">
                                    <select className="form-control font">
                                      <option value>Select Class</option>
                                      <option>1</option>
                                      <option>2</option>
                                      <option>3</option>
                                      <option>4</option>
                                      <option>5</option>
                                      <option>6</option>
                                      <option>7</option>
                                      <option>8</option>
                                      <option>9</option>
                                      <option>10</option>
                                    </select>
                                  </div>
                                </div>
                                <div className="col-sm-12 col-md-4 col-lg-4">
                                  {/* select */}
                                  <div className=" form-group">
                                    <select className="form-control font">
                                      <option value>Select Subject</option>
                                      <option>Bangla</option>
                                      <option>English</option>
                                      <option>Math</option>
                                      <option>Physics</option>
                                      <option>Chemistry</option>
                                    </select>
                                  </div>
                                </div>
                                <div className="col-sm-12 col-md-4 col-lg-4">
                                  <div className=" form-group float-right">
                                    <img
                                      src="img/ok2.png"
                                      className="img-fluid btn p-0"
                                      type="button"
                                    />
                                  </div>
                                </div>
                              </div>
                              {/* PROGRESS PART */}
                              <div className="row p-2 mt-3">
                                <div className="col-12">
                                  <div className="progress-group">
                                    <span className="prog-text">
                                      Total Class
                                    </span>
                                    <span className="float-right tot-value">
                                      50<span className="tot-percent">/70</span>
                                    </span>
                                    <div className="progress progress-sm progress-height">
                                      <div
                                        className="progress-bar progress1 rounded"
                                        style={{ width: "71%" }}
                                      ></div>
                                    </div>
                                  </div>
                                  {/* /.progress-group */}
                                  <div className="progress-group">
                                    <span className="prog-text">
                                      Average Attendance
                                    </span>
                                    <span className="float-right tot-value">
                                      35<span className="tot-percent">/70</span>
                                    </span>
                                    <div className="progress progress-sm progress-height">
                                      <div
                                        className="progress-bar progress2 rounded"
                                        style={{ width: "50%" }}
                                      ></div>
                                    </div>
                                  </div>
                                  {/* /.progress-group */}
                                  <div className="progress-group">
                                    <span className="prog-text">
                                      Average Participation
                                    </span>
                                    <span className="float-right tot-value">
                                      75
                                      <span className="tot-percent">/100</span>
                                    </span>
                                    <div className="progress progress-sm progress-height">
                                      <div
                                        className="progress-bar progress3 rounded"
                                        style={{ width: "75%" }}
                                      ></div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                            {/* attendance right part */}
                            <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                              {/* DONUT CHART START*/}
                              <canvas
                                id="donutChart"
                                style={{
                                  minHeight: 250,
                                  height: 250,
                                  maxHeight: 250,
                                  maxWidth: "100%",
                                }}
                              />
                              {/* DONUT CHART END*/}
                            </div>
                          </div>
                        </div>
                        {/* /.info-box-content */}
                      </div>
                      {/* /.info-box */}
                    </div>
                  </div>
                  {/* attendance full div end */}
                </div>
                {/* right side content */}
                <div className="col-sm-12 col-md-12 col-lg-4">
                  {/* Info boxes */}
                  <div className="row p-2">
                    <div className="col-12 mb-2">
                      <div className="info-box red-border">
                        <div className="info-box-content calender-thumbnail">
                          <img
                            src="img/calender.png"
                            className="calender-img mr-5"
                          />
                          <p className="info-box-text calender-text">
                            Academic Calendar
                          </p>
                        </div>
                        {/* /.info-box-content */}
                      </div>
                      {/* /.info-box */}
                    </div>
                    {/* /.col */}
                    <div className="col-12 mb-2">
                      <div className="info-box red-border">
                        <div className="info-box-content">
                          <span className="info-box-text text-left-margin">
                            <span className="text-live">Live </span> Class
                            Schedule
                          </span>
                        </div>
                        {/* /.info-box-content */}
                      </div>
                      {/* /.info-box */}
                    </div>
                    {/* /.col */}
                    <div className="col-12 mb-2">
                      <div className="info-box red-border">
                        <div className="info-box-content">
                          <span className="info-box-text text-left-margin">
                            Current <span className="text-live">Live </span>
                            Class
                          </span>
                        </div>
                        {/* /.info-box-content */}
                      </div>
                      {/* /.info-box */}
                    </div>
                    {/* /.col */}
                    <div className="col-12 mb-2">
                      <div className="info-box red-border">
                        <div className="info-box-content text-left-margin">
                          <span className="info-box-text">Upcoming Events</span>
                        </div>
                        {/* /.info-box-content */}
                      </div>
                      {/* /.info-box */}
                    </div>
                    {/* /.col */}
                  </div>
                  {/* /.row */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
