import React from "react";
import { Link } from "react-router-dom";
import UserPermission from "./UserPermission";
import PackageSetup from "./PackageSetup";

export default function Settings() {
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Edit start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Settings
                      </h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-md-3">
                          <div
                            className="nav flex-column nav-tabs h-100"
                            id="vert-tabs-tab"
                            role="tablist"
                            aria-orientation="vertical"
                            style={{
                              backgroundColor: "#F8F8F8",
                              paddingTop: "40px",
                            }}
                          >
                            <a
                              className="nav-link active"
                              id="vert-tabs-edit-tab"
                              data-toggle="pill"
                              href="#vert-tabs-edit"
                            >
                              <i className="far fa-user pr-2 pl-3"></i>User
                              Permission
                            </a>
                            <a
                              className="nav-link"
                              id="vert-tabs-profile-tab"
                              data-toggle="pill"
                              href="#vert-tabs-profile"
                              role="tab"
                              aria-controls="vert-tabs-profile"
                              aria-selected="false"
                            >
                              <i className="far fa-list-alt pr-2 pl-3"></i>
                              Package Setup
                            </a>
                          </div>
                        </div>
                        <div
                          className="col-md-9"
                          style={{ padding: "1.25rem" }}
                        >
                          <div
                            className="tab-content"
                            id="vert-tabs-tabContent"
                          >
                            <div
                              className="tab-pane text-left fade show active"
                              id="vert-tabs-edit"
                              role="tabpanel"
                              aria-labelledby="vert-tabs-edit-tab"
                            >
                              <UserPermission />
                            </div>
                            <div
                              className="tab-pane fade"
                              id="vert-tabs-profile"
                              role="tabpanel"
                              aria-labelledby="vert-tabs-profile-tab"
                            >
                              <PackageSetup />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
