import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import PaginationComponent from "../Pagination/PaginationComponent";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableUserList from "./DatatableUserList";
export default function UserListView() {
  const [data, setData] = useState([]);
  const [q, setQ] = useState("");

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllUserNormalList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userListApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userListApiResp - " + JSON.stringify(userListApiResp.data.msg)
      // );
      setData(userListApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  /*test json placeholder api start */
  // useEffect(() => {
  //   const getData = () => {
  //     fetch("https://jsonplaceholder.typicode.com/comments")
  //       .then((response) => response.json())
  //       .then((json) => {
  //         setData(json);
  //       });
  //   };
  //   getData();
  // }, []);
  /*test json placeholder api end */

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 2;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.fname.toLowerCase().includes(search.toLowerCase()) ||
          d.lname.toLowerCase().includes(search.toLowerCase()) ||
          d.email.toLowerCase().includes(search.toLowerCase()) ||
          d.role.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        User List
                      </h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            <Link
                              to="/create-user"
                              className="border ml-1 add-button shadow"
                              title="Create User"
                            >
                              <i className="fas fa-plus add-button-icon"></i>
                            </Link>
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div className="border ml-1 inst-list-search-div">
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          <DatatableUserList data={currentData} />
                          <PaginationComponent
                            total={totalItems}
                            itemsPerPage={ITEMS_PER_PAGE}
                            currentPage={currentPage}
                            onPageChange={(page) => setCurrentPage(page)}
                          />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
