import React, { useState, useEffect } from "react";
import { Link, NavLink } from "react-router-dom";
import { useLocation } from "react-router-dom";
export default function Sidebar() {
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  });
  //console.log(width);
  if (width < 576) {
    var sidebar_mini = document.querySelector(".sidebar-mini"); // Using a class instead, see note below.
    sidebar_mini.classList.add("sidebar-collapse");
  }
  function mySidebar() {
    // alert("hi");
    var menu = document.querySelector(".sidebar-mini"); // Using a class instead, see note below.
    menu.classList.toggle("sidebar-collapse");
  }

  // extract pathname from location
  const { pathname } = useLocation();
  return (
    <React.Fragment>
      {/* LEFT SIDEBAR MENU START */}
      <aside className="main-sidebar sidebar-no-expand elevation-4">
        <div className="sidebar">
          <nav className="mt-2">
            <ul
              className="nav nav-pills nav-sidebar flex-column"
              role="menu"
              data-accordion="false"
            >
              <li className="nav-item">
                <a
                  className="nav-link button-left"
                  role="button"
                  id="sidebar-toggle"
                  onClick={mySidebar}
                >
                  <i className="nav-icon fa fa-fw fa-bars" />
                  <p />
                </a>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/dashboard"
                  className="nav-link"
                  activeclassname="active"
                  exact
                >
                  <i className="nav-icon fas fa-border-all" />
                  <p>Dashboard</p>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/user-list-view"
                  className="nav-link"
                  activeclassname="active"
                  isActive={() =>
                    [
                      "/user-list-view",
                      "/create-user",
                      "/user-detail-view",
                      "/update-user",
                    ].includes(pathname)
                  }
                >
                  <i className="nav-icon far fa-plus-square"></i>
                  <p>Create User</p>
                </NavLink>
              </li>
              {/* <li className="nav-item">
                <NavLink
                  to="/account-info"
                  className="nav-link"
                  activeclassname="active"
                >
                  <i className="nav-icon far fa-address-card"></i>
                  <p>Account Info</p>
                </NavLink>
              </li> */}
              <li className="nav-item dropdown">
                <NavLink
                  to="/institute-list-view"
                  className="nav-link dropbtn"
                  activeclassname="active"
                  isActive={() =>
                    ["/institute-list-view", "/institute-create"].includes(
                      pathname
                    )
                  }
                >
                  <i className="nav-icon far fa-edit" />
                  <p>Registration</p>
                </NavLink>

                {/* <li className="dropdown-content nav-item">
                  <NavLink to="/institute-create" className="nav-link">
                    <i className="nav-icon fas fa-plus"></i>
                    <p className="dropdown-menu-name">Create Institute</p>
                  </NavLink>
                  <a href="#" className="nav-link">
                    <i class="nav-icon far fa-eye"></i>
                    <p
                      className="dropdown-menu-name"
                      style={{
                        color: "#fff",
                        fontSize: "18px",
                      }}
                    >
                      View Institutes
                    </p>
                  </a>
                </li> */}
              </li>
              <li className="nav-item">
                <NavLink
                  to="/package-list-view"
                  className="nav-link dropbtn"
                  activeclassname="active"
                >
                  <i className="nav-icon far fa-copy" />
                  <p>Package System</p>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/package-history"
                  className="nav-link"
                  activeclassname="active"
                >
                  <i className="nav-icon fas fa-history"></i>
                  <p>Package History</p>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/billing-list-view"
                  className="nav-link"
                  activeclassname="active"
                >
                  <i className="nav-icon far fa-file-alt" />
                  <p>Billing System</p>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/payment-list-view"
                  className="nav-link"
                  activeclassname="active"
                >
                  <i className="nav-icon far fa-file-powerpoint" />
                  <p>Payment System</p>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  to="/settings"
                  className="nav-link"
                  activeclassname="active"
                  isActive={() =>
                    [
                      "/settings",
                      "/create-user-type",
                      "/view-user-type",
                      "/edit-user-type",
                    ].includes(pathname)
                  }
                >
                  <i className="nav-icon far fa-sun" />
                  <p>Settings</p>
                </NavLink>
              </li>
            </ul>
          </nav>
        </div>
      </aside>
      {/* LEFT SIDEBAR MENU END */}
    </React.Fragment>
  );
}
