import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function UploadCover() {
  const [coverImg, setCoverImg] = useState("");

  //GET COVER PICTURE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      let id = localStorage.getItem("id");
      var config = {
        method: "get",
        url: "/user-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      let userApiResp = await trackPromise(axios(config));
      userApiResp = userApiResp.data.msg;
      // console.log("userApiResp - " + JSON.stringify(userApiResp.cover_img));
      setCoverImg(userApiResp.cover_img);
    }
    fetchData();
  }, []);
  //GET COVER PICTURE -END

  const imageHandlerCover = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setCoverImg(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  const onSubmitCover = (data) => {
    //console.log(data.cover_img[0]);

    const formData = new FormData();
    formData.append("cover_img", data.cover_img[0]);

    var config = {
      method: "post",
      url: "/cover-img-update?",
      data: formData,
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast("Cover photo uploaded successfully", {
            className: "toast-error",
          });
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };
  return (
    <>
      {/* Upload cover image start */}
      <form onSubmit={handleSubmit(onSubmitCover)}>
        <div className="form-group row" style={{ margin: "24px" }}>
          <div className="col">
            <div style={{ marginTop: "16px", textAlign: "center" }}>
              <div className="img-holder-cover">
                {coverImg ? (
                  <img src={coverImg} id="img" className="imgCover" />
                ) : (
                  <img
                    src={"./img/cover-default.png"}
                    id="img"
                    className="imgCover"
                  />
                )}

                <input
                  type="file"
                  name="cover_img"
                  accept="image/*"
                  id="inputCover"
                  onChange={imageHandlerCover}
                  ref={register({
                    required: "* Please Select a File",
                    validate: (value) => {
                      if (value[0].size > 5000000) {
                        return "Photo size exceeds 5 MB";
                      }
                      return true;
                    },
                  })}
                />
                <label
                  className="image-upload-cover"
                  htmlFor="inputCover"
                  title="Upload Cover Picture"
                >
                  <i className="fas fa-camera"></i>
                </label>
              </div>
              <span className="small">
                Upload Cover Photo (Max: 5 MB | File Type: jpg, png, jpeg, gif)
              </span>
              <br />
              {errors.cover_img && (
                <ErrorMsg>{errors.cover_img.message}</ErrorMsg>
              )}
              <button type="submit" className="btn btn-update mt-3">
                <i className="far fa-caret-square-up mr-3"></i>
                Update
              </button>
            </div>
          </div>
        </div>
      </form>
      {/* Upload cover image end */}
      <LoadingIndicator />
    </>
  );
}
