import React from "react";
import { Link } from "react-router-dom";
import { useLocation } from "react-router";
import UpdateUserProfile from "./UpdateUserProfile";
import UpdateUserAddress from "./UpdateUserAddress";
import UpdateUserAvatar from "./UpdateUserAvatar";
import UpdateUserCover from "./UpdateUserCover";
export default function UpdateUser() {
  const location = useLocation();
  //console.log(location);
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Update start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Update Profile
                      </h4>
                    </div>
                    {/* <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link to="/view-profile">
                        <span
                          className="shadow float-right badge badge-pill badge-bg"
                          style={{ padding: "10px 24px" }}
                        >
                          <i className="far fa-user pr-2"></i>View My Profile
                        </span>
                      </Link>
                    </div> */}
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-md-3">
                          <div
                            className="nav flex-column nav-tabs h-100"
                            id="vert-tabs-tab"
                            role="tablist"
                            aria-orientation="vertical"
                            style={{
                              backgroundColor: "#F8F8F8",
                              paddingTop: "40px",
                            }}
                          >
                            <a
                              className="nav-link"
                              id="vert-tabs-edit-tab"
                              data-toggle="pill"
                              href="#vert-tabs-edit"
                              role="tab"
                              aria-controls="vert-tabs-edit"
                              aria-selected="true"
                            >
                              <i className="far fa-edit pr-2 pl-3"></i>Update
                            </a>
                            <a
                              className="nav-link"
                              id="vert-tabs-profile-tab"
                              data-toggle="pill"
                              href="#vert-tabs-profile"
                              role="tab"
                              aria-controls="vert-tabs-profile"
                              aria-selected="false"
                            >
                              <i className="far fa-address-card pr-2 pl-3"></i>
                              Profile Photo
                            </a>
                            <a
                              className="nav-link"
                              id="vert-tabs-cover-tab"
                              data-toggle="pill"
                              href="#vert-tabs-cover"
                              role="tab"
                              aria-controls="vert-tabs-cover"
                              aria-selected="false"
                            >
                              <i className="far fa-image pr-2 pl-3"></i>Cover
                              Photo
                            </a>
                          </div>
                        </div>
                        <div
                          className="col-md-9"
                          style={{ padding: "1.25rem" }}
                        >
                          <div
                            className="tab-content"
                            id="vert-tabs-tabContent"
                          >
                            <div
                              className="tab-pane text-left fade show active"
                              id="vert-tabs-edit"
                              role="tabpanel"
                              aria-labelledby="vert-tabs-edit-tab"
                            >
                              {/* update option start */}
                              <div className="card-body">
                                <h4>Update Information</h4>
                                <ul
                                  className="nav nav-tabs"
                                  id="custom-content-below-tab"
                                  role="tablist"
                                >
                                  <li className="nav-item">
                                    <a
                                      className="nav-link active border-red"
                                      id="custom-content-below-general-info-tab"
                                      data-toggle="pill"
                                      href="#custom-content-below-general-info"
                                      role="tab"
                                      aria-controls="custom-content-below-general-info"
                                      aria-selected="true"
                                      style={{
                                        padding: "0.5rem 0rem",
                                        marginRight: "40px",
                                      }}
                                    >
                                      <span className="border-bottom-red">
                                        General Information
                                      </span>
                                    </a>
                                  </li>
                                  <li className="nav-item">
                                    <a
                                      className="nav-link border-red"
                                      id="custom-content-below-address-info-tab"
                                      data-toggle="pill"
                                      href="#custom-content-below-address-info"
                                      role="tab"
                                      aria-controls="custom-content-below-address-info"
                                      aria-selected="false"
                                      style={{
                                        padding: "0.5rem 0rem",
                                      }}
                                    >
                                      <span className="border-bottom-red">
                                        Address Information
                                      </span>
                                    </a>
                                  </li>
                                </ul>
                                <div
                                  className="tab-content"
                                  id="custom-content-below-tabContent"
                                >
                                  <div
                                    className="tab-pane fade show active"
                                    id="custom-content-below-general-info"
                                    role="tabpanel"
                                    aria-labelledby="custom-content-below-general-info-tab"
                                  >
                                    <UpdateUserProfile
                                      email={location.state.email}
                                    />
                                  </div>
                                  <div
                                    className="tab-pane fade"
                                    id="custom-content-below-address-info"
                                    role="tabpanel"
                                    aria-labelledby="custom-content-below-address-info-tab"
                                  >
                                    <UpdateUserAddress
                                      email={location.state.email}
                                    />
                                  </div>
                                </div>
                              </div>
                              {/* update option end */}
                            </div>
                            <div
                              className="tab-pane fade"
                              id="vert-tabs-profile"
                              role="tabpanel"
                              aria-labelledby="vert-tabs-profile-tab"
                            >
                              <UpdateUserAvatar email={location.state.email} />
                            </div>
                            <div
                              className="tab-pane fade"
                              id="vert-tabs-cover"
                              role="tabpanel"
                              aria-labelledby="vert-tabs-cover-tab"
                            >
                              <UpdateUserCover email={location.state.email} />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Update end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
