import React, { useState, useEffect } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import DatatableInstDetail from "./DatatableInstDetail";
import { toast } from "react-toastify";
export default function InstituteDetails() {
  //GET INSTITUTE DETAIL -START
  const [instDetail, setInstDetail] = useState({});
  const { id } = useParams();
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { institute_id: id },
      };

      const instDetailApiResp = await trackPromise(axios(config));
      //console.log("instDetailApiResp - " + instDetailApiResp.data.msg);
      setInstDetail(instDetailApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET INSTITUTE DETAIL -END
  //

  let history = useHistory();
  //institute delete
  async function deleteInstitute(instituteId) {
    // console.log("instituteId : " + instituteId);
    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "delete",
      url: `/institute-delete/${instituteId}`,
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "application/json",
      },
    };
    const deleteInstApiResp = await trackPromise(axios(config));
    //console.log("deleteInstApiResp - " + deleteInstApiResp.data);
    if (deleteInstApiResp.data.status === "ok") {
      toast("Institute Deleted Successfully", {
        className: "toast-error",
      });
      history.push("/institute-list-view");
    } else {
      toast(deleteInstApiResp.data.msg, {
        className: "toast-error",
      });
    }
  }
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Institute Detail
                      </h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link to="/institute-list-view">
                        <span
                          className="shadow float-right badge badge-pill badge-bg"
                          style={{ padding: "10px 24px" }}
                        >
                          <i className="far fa-eye pr-2"></i>View All Institute
                        </span>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    {/* /.card-header */}
                    <div className="card-body">
                      <div className="tab-content">
                        <div className="active tab-pane" id="activity">
                          {/* Post */}
                          <div className="post">
                            {/* DISPLAY IN DETAILS FULL INFO OF INSTITUTE  */}
                            <DatatableInstDetail data={instDetail} />
                            <div className="text-center col-sm-12 col-md-8 col-lg-8 col-xl-8">
                              <button
                                className="btn btn-update mt-2 mb-2"
                                onClick={() => deleteInstitute(instDetail.id)}
                              >
                                Delete
                              </button>
                            </div>
                          </div>
                          <LoadingIndicator />
                        </div>
                      </div>
                      {/* /.tab-content */}
                    </div>
                    {/* /.card-body */}

                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
