import React, { useState, useEffect } from "react";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
export default function ViewAddressInfo(props) {
  const [user, setUser] = useState({});

  //const location = useLocation();
  //console.log("Email: " + props.email);

  useEffect(() => {
    let accessToken = localStorage.getItem("token");
    // alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "post",
      url: "/GetNormalUserAddress",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data: {
        email: props.email,
      },
    };

    trackPromise(
      axios(config).then((response) => {
        //console.log(JSON.stringify("Address Info: " + response.data.msg));
        if (response.data.status === "ok") {
          // console.log(response.data.msg);
          setUser(response.data.msg);
        }
      })
    );
  }, []);
  // console.log(user);
  //

  return (
    <>
      <div className="post">
        <table className="table table-borderless table-responsive">
          <tbody>
            <tr>
              <td colSpan="2">
                <b>Present Address</b>
              </td>
            </tr>
            <tr>
              <td>Address</td>
              <td>{user.address}</td>
            </tr>
            <tr>
              <td>Postal Code</td>
              <td>{user.post_code}</td>
            </tr>
            <tr>
              <td>Post Office</td>
              <td>{user.post_office}</td>
            </tr>
            <tr>
              <td>Thana / Upazila</td>
              <td>{user.thana}</td>
            </tr>
            <tr>
              <td>District</td>
              <td>{user.district}</td>
            </tr>
            <tr>
              <td>Division</td>
              <td>{user.division}</td>
            </tr>
            <tr>
              <td colSpan="2">
                <b>Permanent Address</b>
              </td>
            </tr>
            <tr>
              <td>Address</td>
              <td>{user.per_address}</td>
            </tr>
            <tr>
              <td>Postal Code</td>
              <td>{user.per_post_code}</td>
            </tr>
            <tr>
              <td>Post Office</td>
              <td>{user.per_post_office}</td>
            </tr>
            <tr>
              <td>Thana / Upazila</td>
              <td>{user.per_thana}</td>
            </tr>
            <tr>
              <td>District</td>
              <td>{user.per_district}</td>
            </tr>
            <tr>
              <td>Division</td>
              <td>{user.per_division}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </>
  );
}
