import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableInstList from "./DatatableInstList";
export default function InstituteListView() {
  const [data, setData] = useState([]);
  const [q, setQ] = useState("");

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/institutes",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const allPkgApiResp = await trackPromise(axios(config));
      // console.log("allPkgApiResp - " + allPkgApiResp.data.msg.data);
      setData(allPkgApiResp.data.msg.data);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  //search
  function search(rows) {
    const columns = rows[0] && Object.keys(rows[0]);

    return rows.filter((row) =>
      columns.some(
        (column) =>
          row[column].toString().toLowerCase().indexOf(q.toLowerCase()) > -1
      )
    );
  }

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Institute List
                      </h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            <Link
                              to="/institute-create"
                              className="border ml-1 add-button shadow"
                              title="Create Institute"
                            >
                              <i className="fas fa-plus add-button-icon"></i>
                            </Link>
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={q}
                              onChange={(e) => setQ(e.target.value)}
                            />
                            <div className="border ml-1 inst-list-search-div">
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          <DatatableInstList data={search(data)} />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
