import React from "react";
import { Link } from "react-router-dom";
import Moment from "moment";
export default function DatatableInstDetail({ data }) {
  //console.log("Institute Details: " + JSON.stringify(data));

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table col-sm-12 col-md-8 col-lg-8 col-xl-8">
        <tbody>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Institute Name</b>
            </td>
            <td className="inst-list-td text-center">{data.name}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Institute ID</b>
            </td>
            <td className="inst-list-td text-center">{data.institute_id}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Current Status</b>
            </td>
            <td className="inst-list-td text-center">
              <b>{data.active === 1 ? "Active" : "Inactive"}</b>
            </td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Email</b>
            </td>
            <td className="inst-list-td text-center">{data.email}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Phone</b>
            </td>
            <td className="inst-list-td text-center">{data.phone}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Address</b>
            </td>
            <td className="inst-list-td text-center">{data.address}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Subscription Start</b>
            </td>
            <td className="inst-list-td text-center">
              {Moment(data.subscription_s_date).format("DD-MM-YYYY")}
            </td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Subscription End</b>
            </td>
            <td className="inst-list-td text-center">
              {Moment(data.subscription_e_date).format("DD-MM-YYYY")}
            </td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Last Payment</b>
            </td>
            <td className="inst-list-td text-center">
              {Moment(data.last_payment).format("DD-MM-YYYY")}
            </td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Payment Amount</b>
            </td>
            <td className="inst-list-td text-center">{data.payment_amount}</td>
          </tr>
          <tr>
            <td className="inst-list-th text-center inst-list-heading">
              <b>Package Name</b>
            </td>
            <td className="inst-list-td text-center">
              <Link to={`/package-details/${data.package_id}`}>
                {data.package_title}
              </Link>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
