import React from "react";

export default function Datatable({ data }) {
  // console.log("Params: " + JSON.stringify(data));

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Name</th>
            <th className="inst-list-th text-center">Quantity</th>
            <th className="inst-list-th text-center">Price</th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center"> {row["name"]}</td>
              <td className="inst-list-td text-center"> {row["quantity"]}</td>
              <td className="inst-list-td text-center"> {row["price"]}</td>
            </tr>
          ))}
          <tr>
            <td className="inst-list-td text-center" colSpan="2">
              <b>Total</b>
            </td>
            <td className="inst-list-td text-center">
              {/* sum of all quantity */}
              <b>
                {data.reduce(
                  (a, b) => parseInt(a) + parseInt(b["quantity"] || 0),
                  0
                )}
              </b>
            </td>
            <td className="inst-list-td text-center">
              {/* sum of all price */}
              <b>
                {data
                  .reduce(
                    (a, b) => parseFloat(a) + parseFloat(b["price"] || 0),
                    0
                  )
                  .toFixed(2)}
              </b>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
