import React, { useState } from "react";
import { Link } from "react-router-dom";
import Moment from "moment";
export default function DatatablePkgHistoryList({ data }) {
  //pagination
  const [showPerPage, setShowPerPage] = useState(5);
  const [pagination, setPagination] = useState({
    start: 0,
    end: showPerPage,
  });

  const onPaginationChange = (start, end) => {
    // console.log(start, end);
    setPagination({ start: start, end: end });
  };

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Package Name</th>
            <th className="inst-list-th text-center">Display</th>
            <th className="inst-list-th text-center">Description</th>
            <th className="inst-list-th text-center">Created At</th>
            <th className="inst-list-th text-center">Updated At</th>
            <th className="inst-list-th text-center">Action</th>
          </tr>
        </thead>
        <tbody>
          {data.slice(pagination.start, pagination.end).map((row, index) => (
            <tr key={index}>
              {/* this is sl no column. for each tr, 
              pagination.start = 0, 5, 10, 15... and
              index = 0, 1, 2, 3, 4, 5... So
              1st tr => 0+0+1, 0+1+1, 0+2+1.... and
              2nd tr => 5+0+1, 5+1+1, 5+2+1.... and so on*/}
              <td className="inst-list-td text-center">
                {pagination.start + index + 1}
              </td>
              <td className="inst-list-td"> {row["title"]}</td>
              <td className="inst-list-td"> {row["display"]}</td>
              <td className="inst-list-td"> {row["description"]}</td>
              <td className="inst-list-td">
                {Moment.utc(row["created_at"]).format("DD-MM-YYYY")}
              </td>
              <td className="inst-list-td">
                {Moment.utc(row["updated_at"]).format("DD-MM-YYYY")}
              </td>
              <td
                className="inst-list-td text-center action"
                title="View Detail"
              >
                <Link to={`/package-history-detail/${row["id"]}`}>
                  <i className="far fa-eye action-icon"></i>
                </Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
