import React from "react";
import { Link } from "react-router-dom";
import "./password.css";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function ForgetPassword(props) {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook
  const onSubmit = (data) => {
    // console.log(data);
    trackPromise(
      axios.post("/password-reset-request", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok") {
          toast(
            "Password reset link has been sent to your mail.  \nPlease check your mail.",
            { closeOnClick: true, autoClose: false, className: "toast-error" }
          );
          props.history.push("/");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  return (
    <React.Fragment>
      <div className="container-fluid login-page">
        <div className="" style={{ width: "90%", maxWidth: "550px" }}>
          <div className="row m-2 justify-content-center align-items-center">
            <div className="col-12">
              <div className="card shadow">
                <div className="card-body login-card-body">
                  <div className="d-flex flex-column mt-5">
                    <h4 className="font-weight-normal float-left">
                      Forgot your password?
                    </h4>
                    <p className="font-weight-normal text-size float-left">
                      Enter your email below to receive your password <br />
                      <span className="font-weight-normal text-size float-left p-0 m-0">
                        reset instruction
                      </span>
                    </p>
                  </div>

                  <form className="mt-5" onSubmit={handleSubmit(onSubmit)}>
                    <div className="mb-3">
                      <input
                        type="email"
                        className="form-control"
                        name="email"
                        placeholder="Enter your email ID"
                        ref={register({ required: true })}
                      />
                      {errors.email && (
                        <ErrorMsg>* This Field is Required</ErrorMsg>
                      )}
                    </div>

                    <div className="input-group">
                      <button
                        className="btn bg-color shadow text-white float-left mt-5 pr-5 pl-5"
                        type="submit"
                      >
                        Send
                      </button>
                    </div>
                    <p className="mb-5 mt-1 ">
                      <Link to="/" className="small">
                        <i className="fas fa-long-arrow-alt-left mr-2"></i>Back
                        to Login Page
                      </Link>
                    </p>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </React.Fragment>
  );
}
