import React from "react";
import Dashboard from "./Components/Dashboard/Dashboard";
import CreateUser from "./Components/User/CreateUser";
import UserListView from "./Components/User/UserListView";
import UserDetailView from "./Components/User/UserDetailView";
import UpdateUser from "./Components/User/UpdateUser";
import Login from "./Components/Login/Login";
import ForgetPassword from "./Components/Password/ForgetPassword";
import ResetPassword from "./Components/Password/ResetPassword";
import AccountInfo from "./Components/AccountInfo/AccountInfo";
import InstituteCreate from "./Components/InstituteRegistration/InstituteCreate";
import InstituteListView from "./Components/InstituteRegistration/InstituteListView";
import InstituteDetails from "./Components/InstituteRegistration/InstituteDetails";
import InstituteUpdate from "./Components/InstituteRegistration/InstituteUpdate";
import ViewProfile from "./Components/Profile/ViewProfile";
import EditProfile from "./Components/Profile/EditProfile";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import PrivateRoute from "./Components/PrivateRoute/PrivateRoute";
import ErrorHandle from "./Components/ErrorHandling/errorHandle";
import { ToastContainer, Zoom } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CreatePackage from "./Components/PackageSystem/CreatePackage";
import PackageListView from "./Components/PackageSystem/PackageListView";
import PackageDetails from "./Components/PackageSystem/PackageDetails";
import PackageEdit from "./Components/PackageSystem/PackageEdit";
import PackageHistory from "./Components/PackageSystem/PackageHistory";
import PackageHistoryDetail from "./Components/PackageSystem/PackageHistoryDetail";
import Settings from "./Components/Settings/Settings";
import CreateBill from "./Components/BillingSystem/CreateBill";
import UpdateBill from "./Components/BillingSystem/UpdateBill";
import BillingListView from "./Components/BillingSystem/BillingListView";
import BillingInvoiceView from "./Components/BillingSystem/BillingInvoiceView";
import PaymentListView from "./Components/PaymentSystem/PaymentListView";
import PaymentHistoryView from "./Components/PaymentSystem/PaymentHistoryView";
import InstPaymentHistory from "./Components/PaymentSystem/InstPaymentHistory";
import PaymentApprove from "./Components/PaymentSystem/PaymentApprove";
import CreateUserType from "./Components/Settings/CreateUserType";
import ViewUserType from "./Components/Settings/ViewUserType";
import EditUserType from "./Components/Settings/EditUserType";

export default function App() {
  ErrorHandle();
  return (
    <div>
      <BrowserRouter basename="/lms/">
        <div className="wrapper">
          <Switch>
            <Route path="/" exact component={Login} />
            <Route path="/forget-password" component={ForgetPassword} />
            <Route
              path="/password-reset-request/:token"
              component={ResetPassword}
            />
            <PrivateRoute path="/dashboard" component={Dashboard} />
            <PrivateRoute path="/create-user" component={CreateUser} />
            <PrivateRoute path="/user-list-view" component={UserListView} />
            <PrivateRoute path="/user-detail-view" component={UserDetailView} />
            <PrivateRoute path="/update-user" component={UpdateUser} />
            <PrivateRoute path="/account-info" component={AccountInfo} />
            <PrivateRoute
              path="/institute-create"
              component={InstituteCreate}
            />
            <PrivateRoute
              path="/institute-list-view"
              component={InstituteListView}
            />
            <PrivateRoute
              path="/institute-detail/:id"
              component={InstituteDetails}
            />
            <PrivateRoute
              path="/institute-update/:id/:package_id"
              component={InstituteUpdate}
            />
            <PrivateRoute path="/package-create" component={CreatePackage} />
            <PrivateRoute
              path="/package-list-view"
              component={PackageListView}
            />
            <PrivateRoute
              path="/package-details/:id"
              component={PackageDetails}
            />
            <PrivateRoute path="/package-edit/:id" component={PackageEdit} />
            <PrivateRoute path="/package-history" component={PackageHistory} />
            <PrivateRoute
              path="/package-history-detail/:id"
              component={PackageHistoryDetail}
            />
            <PrivateRoute path="/create-bill" component={CreateBill} />
            <PrivateRoute path="/update-bill" component={UpdateBill} />
            <PrivateRoute
              path="/billing-list-view"
              component={BillingListView}
            />
            <PrivateRoute
              path="/billing-invoice-view"
              component={BillingInvoiceView}
            />
            <PrivateRoute
              path="/payment-list-view"
              component={PaymentListView}
            />
            <PrivateRoute
              path="/payment-history-view"
              component={PaymentHistoryView}
            />
            <PrivateRoute
              path="/inst-payment-history"
              component={InstPaymentHistory}
            />
            <PrivateRoute path="/payment-approve" component={PaymentApprove} />

            <PrivateRoute path="/view-profile" component={ViewProfile} />
            <PrivateRoute path="/edit-profile" component={EditProfile} />
            <PrivateRoute path="/settings" component={Settings} />
            <PrivateRoute path="/create-user-type" component={CreateUserType} />
            <PrivateRoute path="/view-user-type" component={ViewUserType} />
            <PrivateRoute path="/edit-user-type" component={EditUserType} />
            <Route path="*" component={() => "404 PAGE NOT FOUND"} />
          </Switch>
          <ToastContainer
            position="top-center"
            autoClose={2000}
            hideProgressBar={true}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            transition={Zoom}
          />
        </div>
      </BrowserRouter>
    </div>
  );
}
