import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function InstituteCreate() {
  const { register, handleSubmit, errors } = useForm();

  //GET ALL PACKAGE NAME -START
  const [allPkgData, setAllPkgData] = useState([]);
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/all-packages",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const allPkgApiResp = await trackPromise(axios(config));
      // console.log("allPkgApiResp - " + allPkgApiResp.data.msg);
      setAllPkgData(allPkgApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END
  let history = useHistory();
  // POST INSTITUTE & PACKAGE DATA -START
  const onSubmit = (data) => {
    // delete id and package_id key from pkgParamList object
    let checkKeyPresenceInArray = (key) =>
      pkgParamList.some((obj) => Object.keys(obj).includes(key));
    var isKeyPresent = checkKeyPresenceInArray("id");
    if (isKeyPresent) {
      pkgParamList.map(function (item) {
        delete item.id;
        delete item.package_id;
        return item;
      });
    }
    //seperate package vlaues from data object start
    const packageData = {
      package: {
        title: data.packageTitle,
        description: data.packageDescription,
        display: data.display,
        type: data.packageType,
      },
      parameters: pkgParamList,
    };
    // console.log("Package Data: " + JSON.stringify(packageData));
    //seperate package vlaues from data object end

    //seperate institute vlaues from data object start
    const instituteData = {
      institute_id: data.institute_id,
      password: data.password,
      name: data.instituteName,
      address: data.address,
      email: data.email,
      phone: data.phone,
      subscription_s_date: data.subscription_s_date,
      subscription_e_date: data.subscription_e_date,
      last_payment: data.last_payment,
      payment_amount: data.payment_amount,
    };
    // console.log("Institute Data: " + JSON.stringify(instituteData));
    //seperate institute vlaues from data object end

    let package_id;
    postData(packageData, instituteData);
    async function postData(packageData, instituteData) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-save",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: packageData,
      };
      const savePkgApiResp = await trackPromise(axios(config));
      //console.log("savePkgApiResp - " + savePkgApiResp.data);
      if (savePkgApiResp.data.status === "ok") {
        package_id = savePkgApiResp.data.msg.package_id;
        // toast("Institute Created Successfully", {
        //   className: "toast-error",
        // });
      } else {
        toast(savePkgApiResp.data.msg, {
          className: "toast-error",
        });
      }

      // console.log("package_id: " + package_id);
      instituteData.package_id = package_id; //get package_id from savePkgApiResp
      var config2 = {
        method: "post",
        url: "/institute-create",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: instituteData,
      };
      const createInstApiResp = await trackPromise(axios(config2));
      //console.log("createInstApiResp - " + createInstApiResp.data);
      if (createInstApiResp.data.status === "ok") {
        toast("Institute Created Successfully", {
          className: "toast-error",
        });
        history.push("/institute-list-view");
      } else {
        toast(createInstApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  };
  // POST INSTITUTE & PACKAGE DATA -END
  // ---------------------------------------------------------------------------------------------
  /*PACKAGE START***************************************************************************************************/
  const [isCreateNewPackage, setIsCreateNewPackage] = useState(false);
  const [isPackageName, setIsPackageName] = useState(false);

  const [pkgTitle, setPkgTitle] = useState("");
  const [pkgDescription, setPkgDescription] = useState("");
  //select a package name and update package title and package description -start
  const handlePackageName = (e) => {
    if (!isPackageName) {
      setIsPackageName(true); //dropdown
    }
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    let pkgId = parseInt(optionElement.getAttribute("data-id"));
    // console.log("PKGID : " + pkgId);
    // console.log("TITLE: " + allPkgData.find((x) => x.id === pkgId).title);
    // console.log(
    //   "DESCRIPTION: " + allPkgData.find((x) => x.id === pkgId).description
    // );

    setPkgTitle(allPkgData.find((x) => x.id === pkgId).title);
    setPkgDescription(allPkgData.find((x) => x.id === pkgId).description);

    // get package parameter (name, price, quantity) by pkgId
    async function fetchData(pkgId) {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/parameters/" + pkgId,
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
      };

      const pkgParamApiResp = await trackPromise(axios(config));
      // console.log("pkgParamApiResp - " + pkgParamApiResp.data.msg);
      if (pkgParamApiResp.data.status === "ok") {
        setPkgParamList(pkgParamApiResp.data.msg);
        // toast("Institute Created Successfully", {
        //   className: "toast-error",
        // });
      } else {
        toast(pkgParamApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
    fetchData(pkgId);
  };
  //select a package name and update package title and package description -end

  /* PACKAGE PARAMETER START*/
  const [pkgParamList, setPkgParamList] = useState([
    { name: "", price: "", quantity: "" },
  ]);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...pkgParamList];
    list[index][name] = value;
    setPkgParamList(list);
    // if (name === "price" || name === "quantity") {
    //   console.log("Executing.......");
    //   const list = [...pkgParamList];
    //   list[index][name] = parseInt(value);
    //   setPkgParamList(list);
    // } else {
    //   const list = [...pkgParamList];
    //   list[index][name] = value;
    //   setPkgParamList(list);
    // }
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...pkgParamList];
    list.splice(index, 1);
    setPkgParamList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setPkgParamList([...pkgParamList, { name: "", price: "", quantity: "" }]);
  };

  /* PACKAGE PARAMETER END*/

  /*PACKAGE END****************************************************************************************************/

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Create an Institute
                      </h4>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* create package form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  ID
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="institute_id"
                                  placeholder="Type of Institute - Institute Name Short Form - Serial Number"
                                  ref={register({
                                    required: "* This Field is Required",
                                    pattern: {
                                      value: /^([A-Z]{2,4}-[A-Z]{2,4}-[0-9]*)$/,
                                      message: "* Invalid Institute ID",
                                    },
                                  })}
                                />
                                <span
                                  title="Ex. UNIV-BRAC-1000"
                                  className="far fa-question-circle field-icon"
                                />
                                {errors.institute_id && (
                                  <ErrorMsg>
                                    {errors.institute_id.message}
                                  </ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Password
                                </label>
                                <br />
                                <input
                                  id="password-field1"
                                  type={passwordShown ? "text" : "password"}
                                  className="form-control"
                                  name="password"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />

                                <span
                                  onClick={togglePasswordVisiblity}
                                  toggle="#password-field1"
                                  className={`fa fa-fw ${
                                    passwordShown ? "fa-eye-slash" : "fa-eye"
                                  } field-icon toggle-password`}
                                />
                                {errors.password && (
                                  <ErrorMsg>{errors.password.message}</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Name
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="instituteName"
                                  ref={register({ required: true })}
                                />
                                {errors.instituteName && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Address
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="address"
                                  ref={register({ required: true })}
                                />
                                {errors.address && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Mail Address
                                </label>
                                <br />
                                <input
                                  type="email"
                                  className="form-control"
                                  name="email"
                                  ref={register({ required: true })}
                                />
                                {errors.email && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Phone No.
                                </label>
                                <br />
                                <input
                                  type="number"
                                  min="0"
                                  className="form-control"
                                  name="phone"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />
                                {errors.phone && (
                                  <ErrorMsg>{errors.phone.message}</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  {isCreateNewPackage ? null : (
                                    <span className="red-star">*</span>
                                  )}
                                  Package Name
                                </label>
                                <button
                                  disabled={isPackageName ? true : false}
                                  onClick={() => setIsCreateNewPackage(true)}
                                  className="shadow float-right badge badge-pill badge-bg mb-3"
                                  style={{ padding: "10px 24px" }}
                                >
                                  Create New Package
                                </button>
                                <br />
                                {/* IF BUTTON PRESS THEN DROPDOWN WILL DISABLE */}
                                {isCreateNewPackage ? (
                                  <select
                                    className="form-control"
                                    name="package"
                                    disabled
                                    onChange={(e) => handlePackageName(e)}
                                    ref={register}
                                  >
                                    <option defaultValue hidden>
                                      --Select Package--
                                    </option>
                                  </select>
                                ) : (
                                  <select
                                    className="form-control"
                                    name="package"
                                    defaultValue="--Select Package--"
                                    onChange={(e) => handlePackageName(e)}
                                    ref={register}
                                  >
                                    <option defaultValue hidden>
                                      --Select Package--
                                    </option>
                                    {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) 
                                    ONLY SHOW DEFAULT PACKAGE*/}
                                    {allPkgData
                                      .sort((a, b) =>
                                        a.title.localeCompare(b.title)
                                      )
                                      .map((e, key) => {
                                        return e.type === "default" ? (
                                          <option
                                            key={key}
                                            data-id={e.id}
                                            value={e.title}
                                          >
                                            {e.title}
                                          </option>
                                        ) : null;
                                      })}
                                  </select>
                                )}
                                {errors.package && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* IF BUTTON PRESS THEN DYNAMICALLY INPUT FIELD CREATED */}
                            {isCreateNewPackage ? (
                              <div>
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Name
                                    </label>
                                    <br />
                                    <input
                                      type="text"
                                      className="form-control"
                                      name="packageTitle"
                                      ref={register({ required: true })}
                                    />
                                    {errors.packageTitle && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Description
                                    </label>
                                    <br />
                                    <textarea
                                      className="form-control"
                                      name="packageDescription"
                                      ref={register({ required: true })}
                                      rows="1"
                                      cols="30"
                                    ></textarea>
                                    {errors.packageDescription && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* PACKAGE IS CREATED FROM BUTTON SO CUSTOM PACKAGE */}
                                <div className="form-group row">
                                  <div className="col">
                                    <input
                                      type="hidden"
                                      className="form-control"
                                      name="packageType"
                                      value="custom"
                                      ref={register}
                                    />
                                  </div>
                                </div>
                              </div>
                            ) : null}
                            {/* IF DROPDOWN SELECTED THEN INPUT WILL AUTO FILLUP AND VALUE WILL BE READONLY */}
                            {isPackageName ? (
                              <div>
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Title
                                    </label>
                                    <br />
                                    <input
                                      type="text"
                                      readOnly
                                      value={pkgTitle}
                                      className="form-control"
                                      name="packageTitle"
                                      ref={register({ required: true })}
                                    />
                                    {errors.packageTitle && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Description
                                    </label>
                                    <br />
                                    <textarea
                                      className="form-control"
                                      name="packageDescription"
                                      readOnly
                                      value={pkgDescription}
                                      ref={register({ required: true })}
                                      rows="1"
                                      cols="30"
                                    ></textarea>
                                    {errors.packageDescription && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* PACKAGE IS CREATED FROM DROPDOWN SO DEFAULT PACKAGE */}
                                <div className="form-group row">
                                  <div className="col">
                                    <input
                                      type="hidden"
                                      className="form-control"
                                      name="packageType"
                                      value="default"
                                      ref={register}
                                    />
                                  </div>
                                </div>
                              </div>
                            ) : null}
                            <div className="form-group row">
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>
                                  Package Display Front-end ?
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      type="radio"
                                      className="mr-2"
                                      value="On"
                                      ref={register({ required: true })}
                                    />
                                    On
                                  </div>
                                  <div className="p-2">
                                    <input
                                      name="display"
                                      className="mr-2"
                                      type="radio"
                                      value="Off"
                                      ref={register({ required: true })}
                                    />
                                    Off
                                  </div>
                                </div>

                                {errors.display && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* IF DROPDOWN SELECTED THEN INPUT WILL AUTO FILLUP AND VALUE WILL BE READONLY */}
                            {isPackageName ? (
                              <div className="form-group">
                                <label>
                                  <span className="red-star">*</span>Package
                                  Parameter
                                  <button
                                    onClick={handleAddClick}
                                    disabled
                                    className="shadow round-plus float-right badge badge-bg mb-3"
                                    style={{ padding: "10px 24px" }}
                                  >
                                    <i className="fas fa-plus"></i>
                                  </button>
                                </label>
                                <br />
                                {pkgParamList.map((x, i) => {
                                  return (
                                    <div className="row" key={i}>
                                      <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                        <input
                                          readOnly
                                          type="text"
                                          className="form-control mb-3"
                                          value={x.name}
                                        />
                                      </div>
                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          readOnly
                                          type="number"
                                          className="form-control mb-3"
                                          value={x.price}
                                        />
                                      </div>
                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          readOnly
                                          type="number"
                                          className="form-control mb-3"
                                          value={x.quantity}
                                        />
                                      </div>
                                      <div className="col-sm-12 col-md-2 col-lg-2 col-xl-2 text-center"></div>
                                    </div>
                                  );
                                })}
                              </div>
                            ) : (
                              <div className="form-group">
                                <label>
                                  <span className="red-star">*</span>Package
                                  Parameter
                                  <span
                                    onClick={handleAddClick}
                                    title="Add New Parameter"
                                    className="shadow round-plus float-right badge badge-bg mb-3"
                                    style={{ padding: "10px 24px" }}
                                  >
                                    <i className="fas fa-plus"></i>
                                  </span>
                                </label>
                                <br />
                                {pkgParamList.map((x, i) => {
                                  return (
                                    <div className="row" key={i}>
                                      <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                        <input
                                          type="text"
                                          className="form-control mb-3"
                                          name="name"
                                          value={x.name}
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          placeholder="Title"
                                          required
                                          ref={register({ required: true })}
                                        />
                                        {errors.name && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          type="number"
                                          className="form-control mb-3"
                                          name="price"
                                          value={x.price}
                                          min="0"
                                          required
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          placeholder="Price"
                                          ref={register({ required: true })}
                                        />
                                        {errors.price && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>
                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <input
                                          type="number"
                                          className="form-control mb-3"
                                          name="quantity"
                                          value={x.quantity}
                                          min="0"
                                          required
                                          onChange={(e) =>
                                            handleInputChange(e, i)
                                          }
                                          placeholder="Quantity"
                                          ref={register({ required: true })}
                                        />
                                        {errors.quantity && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>
                                      <div className="col-sm-12 col-md-2 col-lg-2 col-xl-2 text-center">
                                        {i === 0 ? null : (
                                          <span
                                            onClick={() => handleRemoveClick(i)}
                                            title="Delete This Parameter"
                                            className="shadow round-minus badge badge-bg mb-3"
                                            style={{ padding: "10px 24px" }}
                                          >
                                            <i className="fas fa-minus"></i>
                                          </span>
                                        )}
                                      </div>
                                    </div>
                                  );
                                })}
                              </div>
                            )}
                            {/* -------------------------------------------- */}

                            <div className="form-group row">
                              <div className="col-sm-12 col-md-12 col-lg-8 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>
                                  Subscription Duration
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div>
                                    <input
                                      type="date"
                                      className="form-control"
                                      name="subscription_s_date"
                                      ref={register({ required: true })}
                                    />
                                    {errors.subscription_s_date && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                  <div>
                                    <p className="mt-2 mr-3 ml-3">to</p>
                                  </div>
                                  <div>
                                    <input
                                      type="date"
                                      className="form-control"
                                      name="subscription_e_date"
                                      ref={register({ required: true })}
                                    />
                                    {errors.subscription_e_date && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                              </div>

                              <div className="col-sm-12 col-md-12 col-lg-4 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Last
                                  Payment Date
                                </label>
                                <br />
                                <input
                                  type="date"
                                  className="form-control"
                                  name="last_payment"
                                  ref={register({ required: true })}
                                />
                                {errors.last_payment && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>Payment
                                  Amount
                                </label>
                                <br />
                                <input
                                  type="number"
                                  className="form-control"
                                  name="payment_amount"
                                  min="0"
                                  ref={register({ required: true })}
                                />
                                {errors.payment_amount && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Create
                            </button>
                          </form>
                          {/* create institute form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
