import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";
import Moment from "moment";
export default function DatatablePaymentHistoryList({ data }) {
  //pagination
  const [showPerPage, setShowPerPage] = useState(5);
  const [pagination, setPagination] = useState({
    start: 0,
    end: showPerPage,
  });

  const onPaginationChange = (start, end) => {
    // console.log(start, end);
    setPagination({ start: start, end: end });
  };

  /*TOGGLE INSTITUTE STATUS START*/
  const [isActive, setisActive] = useState(false);
  let status;
  function toggleInstStatus(id) {
    setisActive(isActive ? false : true);
    isActive ? (status = 1) : (status = 0);
    // console.log("STATUS: " + id);
    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-status-update",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          active: status,
        },
      };
      const instStatusApiResp = await trackPromise(axios(config));
      //console.log("instStatusApiResp - " + instStatusApiResp.data);
      if (instStatusApiResp.data.status === "ok") {
        toast(`Institute Status ${status ? "Active" : "Inactive"}`, {
          className: "toast-error",
        });
      } else {
        toast(instStatusApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  /*TOGGLE INSTITUTE STATUS END*/

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Contact</th>
            <th className="inst-list-th text-center">Email</th>
            <th className="inst-list-th text-center">Action</th>
          </tr>
        </thead>
        <tbody>
          {/* CODE WILL COPY FROM DATATABLE INST LIST */}
          <tr>
            <td className="inst-list-td  text-center">1</td>
            <td className="inst-list-td  text-center">UNIV-BRAC-102</td>
            <td className="inst-list-td  text-center">Brac University</td>
            <td className="inst-list-td  text-center">01814757841</td>
            <td className="inst-list-td  text-center">brac.uni@edu.com</td>
            <td className="inst-list-td text-center action">
              <Link to="/inst-payment-history">
                <i className="far fa-eye action-icon"></i>
              </Link>
            </td>
          </tr>
          <tr>
            <td className="inst-list-td  text-center">2</td>
            <td className="inst-list-td  text-center">UNIV-NSU-420</td>
            <td className="inst-list-td  text-center">
              North South University
            </td>
            <td className="inst-list-td  text-center">01814557841</td>
            <td className="inst-list-td  text-center">nsu.uni@.edu.com</td>
            <td className="inst-list-td text-center action">
              <Link to="/inst-payment-history">
                <i className="far fa-eye action-icon"></i>
              </Link>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
