import React, { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function AccountInfo() {
  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  const { register, handleSubmit, errors, watch } = useForm(); // initialise the hook
  const password = useRef({});
  password.current = watch("password", "");

  const onSubmit = (data) => {
    // console.log(data);
    var config = {
      method: "post",
      url: "/password-update",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data,
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast(response.data.msg, {
            className: "toast-error",
          });
        } else {
          toast("The old password you have entered is incorrect", {
            className: "toast-error",
          });
        }
      })
    );
  };

  // -api for get user info
  const [user, setUser] = useState({});

  useEffect(() => {
    let accessToken = localStorage.getItem("token");
    // alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "get",
      url: "/user-detail",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    };

    trackPromise(
      axios(config).then((response) => {
        //console.log(JSON.stringify(response.data.msg));
        if (response.data.status === "ok") {
          // console.log(response.data.msg);
          setUser(response.data.msg);
        }
      })
    );
  }, []);
  //console.log(user);
  //
  /*SHOW OR HIDE PASSWORD START*/
  const [oldPasswordShown, setOldPasswordShown] = useState(false);
  const [newPasswordShown, setNewPasswordShown] = useState(false);
  const [confirmPasswordShown, setConfirmPasswordShown] = useState(false);
  const toggleOldPasswordVisiblity = () => {
    setOldPasswordShown(oldPasswordShown ? false : true);
  };
  const toggleNewPasswordVisiblity = () => {
    setNewPasswordShown(newPasswordShown ? false : true);
  };
  const toggleConfirmPasswordVisiblity = () => {
    setConfirmPasswordShown(confirmPasswordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <h3 className="text-dark mb-4">Account Info</h3>
                  <div className="card card-primary card-outline card-outline-tabs">
                    <div className="card-header p-0 border-bottom-0">
                      <ul
                        className="nav nav-tabs"
                        id="custom-tabs-four-tab"
                        role="tablist"
                      >
                        <li className="nav-item">
                          <a
                            className="nav-link active"
                            id="custom-tabs-four-home-tab"
                            data-toggle="pill"
                            href="#custom-tabs-four-home"
                            role="tab"
                            aria-controls="custom-tabs-four-home"
                            aria-selected="true"
                          >
                            <span className="border-bottom-red">
                              Account Details
                            </span>
                          </a>
                        </li>
                        <li className="nav-item">
                          <a
                            className="nav-link"
                            id="custom-tabs-four-profile-tab"
                            data-toggle="pill"
                            href="#custom-tabs-four-profile"
                            role="tab"
                            aria-controls="custom-tabs-four-profile"
                            aria-selected="false"
                          >
                            <span className="border-bottom-red">
                              Change Password
                            </span>
                          </a>
                        </li>
                      </ul>
                    </div>
                    <div className="card-body">
                      <div
                        className="tab-content"
                        id="custom-tabs-four-tabContent"
                      >
                        <div
                          className="tab-pane fade show active"
                          id="custom-tabs-four-home"
                          role="tabpanel"
                          aria-labelledby="custom-tabs-four-home-tab"
                        >
                          <div className="table-responsive">
                            <table className="table  table-sm table-borderless">
                              <tbody>
                                <tr className="d-flex">
                                  <td className="col-6 col-sm-6 col-md-4 col-lg-3 col-xl-3">
                                    Name
                                  </td>
                                  <td className="col-6 col-sm-6 col-md-8 col-lg-9 col-xl-9">
                                    : {user.fname} {user.lname}
                                  </td>
                                </tr>
                                <tr className="d-flex">
                                  <td className="col-6 col-sm-6 col-md-4 col-lg-3 col-xl-3">
                                    User Name
                                  </td>
                                  <td className="col-6 col-sm-6 col-md-8 col-lg-9 col-xl-9">
                                    : {user.username}
                                  </td>
                                </tr>
                                <tr className="d-flex">
                                  <td className="col-6 col-sm-6 col-md-4 col-lg-3 col-xl-3">
                                    User Type
                                  </td>
                                  <td className="col-6 col-sm-6 col-md-8 col-lg-9 col-xl-9">
                                    : {user.role}
                                  </td>
                                </tr>
                                <tr className="d-flex">
                                  <td className="col-6 col-sm-6 col-md-4 col-lg-3 col-xl-3">
                                    Password
                                  </td>
                                  <td className="col-6 col-sm-6 col-md-8 col-lg-9 col-xl-9">
                                    : ********
                                  </td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                        <div
                          className="tab-pane fade"
                          id="custom-tabs-four-profile"
                          role="tabpanel"
                          aria-labelledby="custom-tabs-four-profile-tab"
                        >
                          {/* input field */}
                          {/* form start */}
                          <form
                            className="form-horizontal"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="card-body padding-left">
                              <div className="form-group row">
                                <div className="col max-width">
                                  <label>Type Your Old Password</label>
                                  <br />
                                  <input
                                    id="password-field1"
                                    type={
                                      oldPasswordShown ? "text" : "password"
                                    }
                                    className="form-control"
                                    name="old_password"
                                    ref={register({
                                      required: "* This Field is Required",
                                    })}
                                  />

                                  <span
                                    onClick={toggleOldPasswordVisiblity}
                                    toggle="#password-field1"
                                    className={`fa fa-fw ${
                                      oldPasswordShown
                                        ? "fa-eye-slash"
                                        : "fa-eye"
                                    } field-icon toggle-password`}
                                  />
                                  {errors.old_password && (
                                    <ErrorMsg>
                                      {errors.old_password.message}
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                              <div className="form-group row">
                                <div className="col max-width">
                                  <label>Type New Password</label>
                                  <br />
                                  <input
                                    id="password-field2"
                                    type={
                                      newPasswordShown ? "text" : "password"
                                    }
                                    className="form-control"
                                    name="password"
                                    ref={register({
                                      required: "* This Field is Required",
                                      minLength: {
                                        value: 8,
                                        message:
                                          "* Minimum 8 Character Required",
                                      },
                                    })}
                                  />

                                  <span
                                    onClick={toggleNewPasswordVisiblity}
                                    toggle="#password-field2"
                                    className={`fa fa-fw ${
                                      newPasswordShown
                                        ? "fa-eye-slash"
                                        : "fa-eye"
                                    } field-icon toggle-password`}
                                  />
                                  {errors.password && (
                                    <ErrorMsg>
                                      {errors.password.message}
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                              <div className="form-group row">
                                <div className="col max-width">
                                  <label>Confirm Password</label>
                                  <br />
                                  <input
                                    id="password-field3"
                                    type={
                                      confirmPasswordShown ? "text" : "password"
                                    }
                                    className="form-control"
                                    name="password_confirmation"
                                    ref={register({
                                      required: "* This Field is Required",
                                      minLength: {
                                        value: 8,
                                        message:
                                          "* Minimum 8 Character Required",
                                      },
                                      validate: (value) =>
                                        value === password.current ||
                                        "Both Password Should Match",
                                    })}
                                  />
                                  <span
                                    onClick={toggleConfirmPasswordVisiblity}
                                    toggle="#password-field3"
                                    className={`fa fa-fw ${
                                      confirmPasswordShown
                                        ? "fa-eye-slash"
                                        : "fa-eye"
                                    } field-icon toggle-password`}
                                  />
                                  {errors.password_confirmation && (
                                    <ErrorMsg>
                                      {errors.password_confirmation.message}
                                    </ErrorMsg>
                                  )}
                                </div>
                              </div>
                              <button type="submit" className="btn btn-update">
                                Update
                              </button>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
      <LoadingIndicator />
    </React.Fragment>
  );
}
