import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
export default function PackageHistoryDetail() {
  //GET ALL PACKAGE NAME -START
  const [packageDetail, setPackageDetail] = useState([]);
  const [packageTitle, setPackageTitle] = useState("");
  const { id } = useParams();
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          package_id: id,
        },
      };

      const pkgDetailApiResp = await trackPromise(axios(config));
      // console.log("pkgDetailApiResp - " + pkgDetailApiResp.data.msg.params);
      setPackageTitle(pkgDetailApiResp.data.msg.title);
      setPackageDetail(pkgDetailApiResp.data.msg.params);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END
  //

  return (
    <div className="wrapper">
      {/* PAGE CONTENT START */}
      <div className="content-wrapper pt-3 pb-5">
        <div className="content">
          <div className="container-fluid layout-width">
            {/*  */}
            {/* Content Start */}
            <div className="row">
              <div className="col-12">
                <div className="row">
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <h4 style={{ marginBottom: "32px", color: "black" }}>
                      Package Parameter History
                    </h4>
                  </div>
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                    <Link to="/package-history">
                      <span
                        className="shadow float-right badge badge-pill badge-bg"
                        style={{ padding: "10px 24px" }}
                      >
                        <i className="far fa-eye pr-2"></i>View Package History
                      </span>
                    </Link>
                  </div>
                </div>
                <div className="card">
                  <div className="card-body p-0">
                    <div className="row" style={{ minHeight: "70vh" }}>
                      <div className="col-12" style={{ padding: "2rem" }}>
                        <div className="col-sm-12 col-md-8 col-lg-8 col-xl-8 text-center">
                          <b>{packageTitle}</b>
                        </div>
                        <div className="table-responsive">
                          <table className="table table-hover institute-table col-sm-12 col-md-8 col-lg-8 col-xl-8">
                            <thead>
                              <tr className="inst-list-heading">
                                <th className="inst-list-th text-center">
                                  SL No.
                                </th>
                                <th className="inst-list-th text-center">
                                  Name
                                </th>
                                <th className="inst-list-th text-center">
                                  Quantity
                                </th>
                                <th className="inst-list-th text-center">
                                  Price
                                </th>
                                <th className="inst-list-th text-center">
                                  Created At
                                </th>
                                <th className="inst-list-th text-center">
                                  Updated At
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              {packageDetail.map((row, index) => (
                                <tr key={index}>
                                  <td className="inst-list-td text-center">
                                    {index + 1}
                                  </td>
                                  <td className="inst-list-td text-center">
                                    {row["name"]}
                                  </td>
                                  <td className="inst-list-td text-center">
                                    {row["quantity"]}
                                  </td>
                                  <td className="inst-list-td text-center">
                                    {row["price"]}
                                  </td>
                                  <td className="inst-list-td text-center"></td>
                                  <td className="inst-list-td text-center"></td>
                                </tr>
                              ))}
                            </tbody>
                          </table>
                        </div>
                        <LoadingIndicator />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
