import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import ViewAddressInfo from "./ViewAddressInfo";
import Moment from "moment";
export default function ViewProfile() {
  const [user, setUser] = useState({});

  useEffect(() => {
    let accessToken = localStorage.getItem("token");
    // alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    var config = {
      method: "get",
      url: "/user-detail",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    };
    var response = {};

    trackPromise(
      axios(config).then((response) => {
        //console.log(JSON.stringify(response.data.msg));
        if (response.data.status === "ok") {
          // console.log(response.data.msg);
          setUser(response.data.msg);
        }
      })
    );
  }, []);
  //console.log(user);
  //
  //format date => 09-09-2020 to 9 Sep, 2020
  function formatDate(date) {
    if (date !== undefined && date !== "") {
      var myDate = new Date(date);
      var month = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ][myDate.getMonth()];
      var str = myDate.getDate() + " " + month + ", " + myDate.getFullYear();
      return str;
    }
    return "";
  }
  return (
    <React.Fragment>
      <div className="wrapper">
        <div className="content-wrapper">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              {/* Profile header section start */}
              <div className="row">
                <div className="col-12 pl-3">
                  {/* Widget: user widget style 1 */}
                  <div className="card card-widget widget-user">
                    {/* Add the bg color to the header using any of the bg-* classes */}

                    {(() => {
                      if (user.cover_img) {
                        return (
                          <div
                            className="widget-user-header text-white"
                            style={{
                              background: `url('${user.cover_img}') center center`,
                            }}
                          ></div>
                        );
                      } else {
                        return (
                          <div
                            className="widget-user-header text-white"
                            style={{
                              background:
                                "url('dist/img/cover_default.jpg') center center",
                            }}
                          ></div>
                        );
                      }
                    })()}

                    <div className="widget-user-image">
                      {(() => {
                        if (user.avatar_img) {
                          return (
                            <img
                              className="img-fluid"
                              src={`${user.avatar_img}`}
                            />
                          );
                        } else {
                          return (
                            <img
                              className="img-fluid"
                              src="dist/img/avatar_default.jpg"
                              alt="User Avatar"
                            />
                          );
                        }
                      })()}
                    </div>
                    <div className="user-info">
                      {(() => {
                        if (
                          user.fname &&
                          user.lname &&
                          user.role &&
                          user.created_at
                        ) {
                          return (
                            <>
                              <h3 className="info-color">
                                {user.fname} {user.lname}
                                <span className="badge badge-pill badge-bg pr-2 pl-2 pt-1 pb-1">
                                  {user.role}
                                </span>
                              </h3>
                              <h6 className="info-color">
                                Date of Joining: {formatDate(user.created_at)}
                              </h6>
                            </>
                          );
                        } else {
                          return (
                            <>
                              <h3
                                className="info-color"
                                style={{ paddingTop: "70px" }}
                              >
                                <span className="badge badge-pill badge-bg"></span>
                              </h3>
                              <h6 className="info-color"></h6>
                            </>
                          );
                        }
                      })()}
                    </div>
                    <div className="card-footer bg-white">
                      <div className="row">
                        <div className="col">
                          <div className="description-block"></div>
                          {/* /.description-block */}
                        </div>
                      </div>
                      {/* /.row */}
                    </div>
                  </div>
                  {/* /.widget-user */}
                </div>
              </div>
              {/* Profile header section end */}
              {/* Profile View start */}
              <div className="row">
                <div className="col-12 pl-3">
                  <div className="col p-0">
                    <div className="card">
                      <div className="card-header p-2">
                        <div className="row">
                          <div className="col-6">
                            <h4
                              style={{
                                paddingLeft: "16px",
                                paddingTop: "10px",
                                color: "black",
                                marginLeft: "5px",
                              }}
                            >
                              View Profile
                            </h4>
                          </div>
                          <div className="col-6" style={{ paddingTop: "8px" }}>
                            <Link to="/edit-profile">
                              <span
                                className="shadow float-right badge badge-pill badge-bg"
                                style={{ padding: "10px 24px" }}
                              >
                                <i className="far fa-edit pr-2"></i>Edit Profile
                              </span>
                            </Link>
                          </div>
                        </div>
                      </div>
                      {/* /.card-header */}
                      <div className="card-body">
                        <div className="tab-content">
                          <div className="active tab-pane" id="activity">
                            {/* Post */}
                            <p className="title-border">General Information</p>
                            <div className="post">
                              <table className="table table-borderless table-responsive">
                                <tbody>
                                  <tr>
                                    <td>First Name</td>
                                    <td>{user.fname}</td>
                                  </tr>
                                  <tr>
                                    <td>Last Name</td>
                                    <td>{user.lname}</td>
                                  </tr>
                                  <tr>
                                    <td>Date of Birth</td>
                                    <td>
                                      {Moment(
                                        new Date(user.date_of_birth)
                                      ).format("DD/MM/YYYY")}
                                    </td>
                                  </tr>
                                  <tr>
                                    <td>Gender</td>
                                    <td>{user.gender}</td>
                                  </tr>
                                  <tr>
                                    <td>Nationality</td>
                                    <td>{user.nationality}</td>
                                  </tr>
                                  <tr>
                                    <td>Mobile No.</td>
                                    <td>{user.mobile}</td>
                                  </tr>
                                  <tr>
                                    <td>Email</td>
                                    <td>{user.email}</td>
                                  </tr>
                                  <tr>
                                    <td>Designation</td>
                                    <td>{user.designation}</td>
                                  </tr>
                                  <tr>
                                    <td>Place of Birth</td>
                                    <td>{user.place_of_birth}</td>
                                  </tr>
                                  <tr>
                                    <td>Marital Status</td>
                                    <td>{user.marital_status}</td>
                                  </tr>
                                  <tr>
                                    <td>Religion</td>
                                    <td>{user.religion}</td>
                                  </tr>
                                  <tr>
                                    <td>NID</td>
                                    <td>{user.nid}</td>
                                  </tr>
                                  <tr>
                                    <td>Employee ID</td>
                                    <td>{user.id}</td>
                                  </tr>
                                </tbody>
                              </table>
                            </div>
                            {/* /.post */}
                            <p className="title-border">Address Information</p>
                            <ViewAddressInfo />
                          </div>
                        </div>
                        {/* /.tab-content */}
                      </div>
                      {/* /.card-body */}
                    </div>
                    {/* /.nav-tabs-custom */}
                  </div>
                </div>
                {/* /.col */}
              </div>
              {/* Profile View end */}
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
      <LoadingIndicator />
    </React.Fragment>
  );
}
