import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function CreateUserForm() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let history = useHistory();
  const onSubmit = (data) => {
    console.log(data);
    // var config = {
    //   method: "post",
    //   url: "/info-update",
    //   headers: {
    //     Authorization: `Bearer ${accessToken}`,
    //   },
    //   data,
    // };

    // trackPromise(
    //   axios(config).then(function (response) {
    //     // console.log(JSON.stringify(response.data));
    //     if (response.data.status === "ok") {
    //       toast(response.data.msg, {
    //         className: "toast-error",
    //       });
    //       setIsProfileUpdate(true);
    //       //history.push("/view-profile");
    //     } else {
    //       toast(response.data.msg, {
    //         className: "toast-error",
    //       });
    //     }
    //   })
    // );
  };

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/
  return (
    <div>
      {/* create form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> First Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="fname"
              ref={register({ required: true })}
            />
            {errors.fname && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Last Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="lname"
              ref={register}
            />
          </div>
        </div>

        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Email
            </label>
            <br />
            <input
              type="email"
              className="form-control"
              name="email"
              ref={register({
                required: "* This Field is Required",
              })}
            />
            {errors.email && <ErrorMsg>{errors.email.message}</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Mobile No.
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="mobile"
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^(?:\+88|01)?\d{11}\r?$/,
                  message: "* Please Enter Number",
                },
              })}
            />
            {errors.mobile && <ErrorMsg>{errors.mobile.message}</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>User Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="username"
              ref={register({ required: true })}
            />
            {errors.username && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>User Type
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
            >
              <option hidden defaultValue>
                ---Select---
              </option>
              <option value="Billing">Billing Admin</option>
              <option value="FemaPaymentle">Payment Admin</option>
              <option value="Package">Package Admin</option>
              <option value="Super">Super Admin</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>Password
            </label>
            <br />
            <input
              id="password-field1"
              type={passwordShown ? "text" : "password"}
              className="form-control"
              name="password"
              ref={register({
                required: "* This Field is Required",
              })}
            />

            <span
              onClick={togglePasswordVisiblity}
              toggle="#password-field1"
              className={`fa fa-fw ${
                passwordShown ? "fa-eye-slash" : "fa-eye"
              } field-icon toggle-password`}
            />
            {errors.password && <ErrorMsg>{errors.password.message}</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>Date of Birth
            </label>
            <br />
            <input
              type="date"
              className="form-control"
              name="date_of_birth"
              ref={register({ required: true })}
            />
            {errors.date_of_birth && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>Gender
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
            >
              <option hidden defaultValue>
                ---Select---
              </option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Create
        </button>
      </form>
      {/* create form general info end */}
      <LoadingIndicator />
    </div>
  );
}
