import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function UpdateAddress() {
  let accessToken = localStorage.getItem("token");
  let user_id = localStorage.getItem("id");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);
  const { register, handleSubmit, errors } = useForm(); // initialise the hook
  let history = useHistory();
  const onSubmit = (data) => {
    // console.log(data);
    data["user_id"] = user_id;
    var config = {
      method: "post",
      url: "/update-user-address",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data,
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast(response.data.msg, {
            className: "toast-error",
          });
          history.push("/view-profile");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  /*CASCADING DROPDOWN DIVISION, DISTRICT, THANA/UPAZILA START */
  const [user, setUser] = useState({});
  const [divisionData, setDivisionData] = useState([]);
  const [districtData, setDistrictData] = useState([]);
  const [thanaUpazilaData, setThanaUpazila] = useState([]);

  const [perDivisionData, setPerDivisionData] = useState([]);
  const [perDistrictData, setPerDistrictData] = useState([]);
  const [perThanaUpazilaData, setPerThanaUpazila] = useState([]);

  const [isSameAsPresent, setIsSameAsPresent] = useState(false);

  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/user-address",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userApiResp = await trackPromise(axios(config));
      //console.log("TEST user - " + userApiResp.data.msg);
      setUser(userApiResp.data.msg);

      const divisionUrl =
        "http://35.226.65.139:8080/covidcommunityservices/api/geocode/division";

      const divisionApiResp = await trackPromise(axios.get(divisionUrl));
      //console.log("TEST division - " + divisionApiResp.data);
      setDivisionData(divisionApiResp.data);
      setPerDivisionData(divisionApiResp.data);

      //console.log("TEST user.divison - " + userApiResp.data.msg.division); //------------------------------------------------

      let divisionArray = []; //store divison in array
      divisionApiResp.data
        .sort((a, b) => parseInt(a.id, 10) - parseInt(b.id, 10))
        .map((e, key) => {
          //console.log("TEST name - " + e.name);
          divisionArray.push(e.name);
        });
      //console.log("divisionArray - " + divisionArray);
      // console.log(
      //   "divisionId - " +
      //     (divisionArray.indexOf(userApiResp.data.msg.division) + 1)
      // ); //dhaka - [dhaka, khulna, barisal]

      let divisionId = divisionArray.indexOf(userApiResp.data.msg.division) + 1;

      const districtUrl =
        "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
        divisionId;
      const districtApiResp = await trackPromise(axios.get(districtUrl));
      //console.log("TEST district - " + districtApiResp.data);
      setDistrictData(districtApiResp.data);
      setPerDistrictData(districtApiResp.data);

      // console.log("TEST user.district - " + userApiResp.data.msg.district); //------------------------------------------------

      let districtArray = []; //store divison in array
      districtApiResp.data
        .sort((a, b) => parseInt(a.id, 10) - parseInt(b.id, 10))
        .map((e, key) => {
          //console.log("TEST name - " + e.name);
          districtArray.push(e.name);
        });
      //console.log("districtArray - " + districtArray);
      // console.log(
      //   "districtId - " +
      //     (districtArray.indexOf(userApiResp.data.msg.district) + 1)
      // ); //dhaka - [dhaka, khulna, barisal]

      let districtId = districtArray.indexOf(userApiResp.data.msg.district) + 1;

      const thanaUpazilaUrl =
        "http://35.226.65.139:8080/covidcommunityservices/api/geocode/upazilas/" +
        districtId;
      const thanaUpazilaApiResp = await trackPromise(
        axios.get(thanaUpazilaUrl)
      );
      // let response = thanaUpazilaApiResp.text();

      // console.log(
      //   "TEST thana/upazila - " + JSON.stringify(thanaUpazilaApiResp)
      // );
      setThanaUpazila(thanaUpazilaApiResp.data);
      setPerThanaUpazila(thanaUpazilaApiResp.data);
    }
    fetchData();
  }, []);

  //onchange division dropdown menu, district value will update -start
  const ChangeDivision = (e) => {
    setUser({ ...user, division: e.target.value });
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    var divisionId = optionElement.getAttribute("data-id");
    //console.log("vlaue of e : " + divisionId);

    trackPromise(
      axios
        .get(
          "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
            divisionId
        )
        .then((response) => {
          //console.log("District Data: " + response.data);
          setDistrictData(response.data);
        })
    );
  };
  //onchange division dropdown menu, district value will update -end

  //onchange district dropdown menu, thana/upazila value will update -start
  const ChangeDistrict = (e) => {
    setUser({ ...user, district: e.target.value });
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    var districtId = optionElement.getAttribute("data-id");
    //console.log("vlaue of e : " + districtId);

    trackPromise(
      axios
        .get(
          "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
            districtId
        )
        .then((response) => {
          //console.log("Thana/Upazila Data: " + response.data);
          setThanaUpazila(response.data);
        })
    );
  };
  //onchange district dropdown menu, thana/upazila value will update -end

  //onchange thana/upazila dropdown menu -start
  const ChangeThanaUpazila = (e) => {
    setUser({ ...user, thana: e.target.value });
    //console.log("Thana/Upazila onchange method executing.....");
  };
  //onchange thana/upazila dropdown menu -end

  //onchange perDivision dropdown menu, perDistrict value will update -start
  const ChangePerDivision = (e) => {
    setUser({ ...user, per_division: e.target.value });
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    var perDivisionId = optionElement.getAttribute("data-id");
    // console.log("vlaue of e : " + perDivisionId);

    trackPromise(
      axios
        .get(
          "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
            perDivisionId
        )
        .then((response) => {
          //console.log("Per District Data: " + response.data);
          setPerDistrictData(response.data);
        })
    );
  };
  //onchange perDivision dropdown menu, perDistrict value will update -end

  //onchange district dropdown menu, thana/upazila value will update -start
  const ChangePerDistrict = (e) => {
    setUser({ ...user, per_district: e.target.value });
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    var perDistrictId = optionElement.getAttribute("data-id");
    //console.log("vlaue of e : " + perDistrictId);

    trackPromise(
      axios
        .get(
          "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
            perDistrictId
        )
        .then((response) => {
          //console.log("Per Thana/Upazila Data: " + response.data);
          setPerThanaUpazila(response.data);
        })
    );
  };
  //onchange district dropdown menu, thana/upazila value will update -end

  //onchange thana/upazila dropdown menu -start
  const ChangePerThanaUpazila = (e) => {
    setUser({ ...user, per_thana: e.target.value });
    //console.log("Per Thana/Upazila onchange method executing.....");
  };
  //onchange thana/upazila dropdown menu -end

  async function fetchData(divisionId, districtId) {
    //console.log("DIV- " + divisionId + " DIS- " + districtId);
    const districtUrl =
      "http://35.226.65.139:8080/covidcommunityservices/api/geocode/districts/" +
      divisionId;
    const districtApiResp = await trackPromise(axios.get(districtUrl));
    //console.log("TEST district - " + districtApiResp.data);
    setPerDistrictData(districtApiResp.data);

    const thanaUpazilaUrl =
      "http://35.226.65.139:8080/covidcommunityservices/api/geocode/upazilas/" +
      districtId;
    const thanaUpazilaApiResp = await trackPromise(axios.get(thanaUpazilaUrl));
    setPerThanaUpazila(thanaUpazilaApiResp.data);
  }
  //ChangeDivisionIsSame -start
  const ChangeDivisionIsSame = (divisionId, districtId) => {
    fetchData(divisionId, districtId);
  };
  //ChangeDivisionIsSame -end

  //onchange is same present -start
  const ChangeIsSameAsPresent = (e) => {
    setIsSameAsPresent(e.target.checked);
    // setUser({ ...user, per_thana: e.target.value });
    // console.log("CAlled---------" + e.target.checked);
    if (e.target.checked) {
      let address = document.querySelector("#address").value;
      let post_code = document.querySelector("#post_code").value;
      let post_office = document.querySelector("#post_office").value;
      let division = document.querySelector("#division").value;
      let district = document.querySelector("#district").value;
      let thana = document.querySelector("#thana").value;

      // document.querySelector("#per_address").value = address;
      // document.querySelector("#per_post_code").value = post_code;
      // document.querySelector("#per_post_office").value = post_office;
      // document.querySelector("#per_division").value = division;
      // document.querySelector("#per_district").value = district;
      // document.querySelector("#per_thana").value = thana;

      // alert(division + district + thana);

      //get division and district id
      let index = document.querySelector("#division").selectedIndex;
      let optionElement = document.querySelector("#division").childNodes[index];
      let divisionId = optionElement.getAttribute("data-id");
      let index2 = document.querySelector("#district").selectedIndex;
      let optionElement2 = document.querySelector("#district").childNodes[
        index2
      ];
      let districtId = optionElement2.getAttribute("data-id");
      // console.log("DIV- " + divisionId + " DIS- " + districtId);
      ChangeDivisionIsSame(divisionId, districtId);

      setUser({
        ...user,
        per_address: address,
        per_post_code: post_code,
        per_post_office: post_office,
        per_division: division,
        per_district: district,
        per_thana: thana,
      });

      // setTimeout(() => {
      //   alert(JSON.stringify(user));
      // }, 2000);

      // console.log(
      //   "Changed Value========== " +
      //     address +
      //     "-" +
      //     post_code +
      //     "-" +
      //     post_office +
      //     "-" +
      //     division +
      //     "-" +
      //     district +
      //     "-" +
      //     thana
      // );
    }
  };
  //onchange is same present -end

  /*CASCADING DROPDOWN DIVISION, DISTRICT, THANA/UPAZILA END */

  return (
    <>
      {/* edit form address info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        <h5>Present Address</h5>
        <div className="form-group row">
          <div className="col">
            <label>Address</label>
            <br />
            <input
              type="text"
              id="address"
              className="form-control"
              name="address"
              defaultValue={user.address}
              ref={register({ required: true })}
            />
            {errors.address && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Postal Code</label>
            <br />
            <input
              type="number"
              id="post_code"
              min="0"
              className="form-control"
              name="post_code"
              defaultValue={user.post_code}
              ref={register({ required: true })}
            />
            {errors.post_code && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Post Office</label>
            <br />
            <input
              type="text"
              id="post_office"
              className="form-control"
              name="post_office"
              defaultValue={user.post_office}
              ref={register({ required: true })}
            />
            {errors.post_office && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>Division</label>
            <br />
            {divisionData.length === 0 ? null : (
              <select
                className="form-control"
                id="division"
                name="division"
                value={user.division}
                onChange={(e) => ChangeDivision(e)}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {divisionData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.division && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>District</label>
            <br />
            {districtData.length === 0 ? null : (
              <select
                className="form-control"
                name="district"
                id="district"
                onChange={(e) => ChangeDistrict(e)}
                value={user.district}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {districtData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.district && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Thana / Upazila</label>
            <br />
            {thanaUpazilaData.length === 0 ? null : (
              <select
                className="form-control"
                name="thana"
                id="thana"
                onChange={(e) => ChangeThanaUpazila(e)}
                value={user.thana}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {thanaUpazilaData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.thana && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="d-flex flex-sm-row flex-column">
          <div className="mr-auto">
            <h5>Permanent Address</h5>
          </div>
          <div>
            <div className="form-check float-right">
              <input
                className="form-check-input check-input"
                type="checkbox"
                onChange={(e) => ChangeIsSameAsPresent(e)}
                checked={isSameAsPresent}
                id="defaultCheck1"
              />
              <label className="form-check-label" htmlFor="defaultCheck1">
                Same as Present Address
              </label>
            </div>
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <div className="row pb-2">
              <div className="col">
                <label>Address</label>
              </div>
            </div>
            <input
              type="text"
              className="form-control"
              id="per_address"
              name="per_address"
              defaultValue={user.per_address}
              ref={register({ required: true })}
            />
            {errors.per_address && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Postal Code</label>
            <br />
            <input
              type="number"
              min="0"
              className="form-control"
              id="per_post_code"
              name="per_post_code"
              defaultValue={user.per_post_code}
              ref={register({ required: true })}
            />
            {errors.per_post_code && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Post Office</label>
            <br />
            <input
              type="text"
              className="form-control"
              id="per_post_office"
              name="per_post_office"
              defaultValue={user.per_post_office}
              ref={register({ required: true })}
            />
            {errors.per_post_office && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>

        <div className="form-group row">
          <div className="col">
            <label>Division</label>
            <br />
            {perDivisionData.length === 0 ? null : (
              <select
                className="form-control"
                name="per_division"
                id="per_division"
                value={user.per_division}
                onChange={(e) => ChangePerDivision(e)}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {perDivisionData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.per_division && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>District</label>
            <br />
            {perDistrictData.length === 0 ? null : (
              <select
                className="form-control"
                name="per_district"
                id="per_district"
                value={user.per_district}
                onChange={(e) => ChangePerDistrict(e)}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {perDistrictData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.per_district && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        <div className="form-group row">
          <div className="col">
            <label>Thana / Upazila</label>
            <br />
            {perThanaUpazilaData.length === 0 ? null : (
              <select
                className="form-control"
                name="per_thana"
                id="per_thana"
                onChange={(e) => ChangePerThanaUpazila(e)}
                value={user.per_thana}
                ref={register({ required: true })}
              >
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {perThanaUpazilaData
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.name}>
                        {e.name}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.per_thana && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Save
        </button>
      </form>
      {/* edit form address info end */}
      <LoadingIndicator />
    </>
  );
}
