import "./login.css";
import React, { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Footer from "../Layout/Footer";
import logo from "../Layout/logo2.png";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function Login(props) {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  /*REMEMBER ME START*/
  const [isRememberMe, setIsRememberMe] = useState(false);
  /*REMEMBER ME END*/

  const onSubmit = (data) => {
    console.log("Username & Password " + JSON.stringify(data));
    trackPromise(
      axios.post("/authenticate", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok") {
          localStorage.setItem(
            "token",
            JSON.stringify(response.data.msg.token)
          );
          localStorage.setItem("id", JSON.stringify(response.data.msg.id));
          //console.log(this.props.history.push("/Dashboard"));
          if (isRememberMe) {
            localStorage.setItem("email", data.email);
            localStorage.setItem("password", data.password);
          } else {
            localStorage.removeItem("email");
            localStorage.removeItem("password");
          }
          props.history.push("/dashboard");
        } else {
          toast("Wrong Email Or Password", {
            className: "toast-error",
          });
        }
      })
    );
  };

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  return (
    <React.Fragment>
      <div className="wrapper login-page">
        <div className="" style={{ width: "90%", maxWidth: "550px" }}>
          <div className="row m-2 justify-content-center align-items-center">
            <div className="col-12">
              <div className="card shadow">
                <div className="card-body login-card-body">
                  <div className="d-flex flex-column mt-5">
                    <h2 className="font-weight-normal text-center">
                      <img
                        src={logo}
                        alt="Babylon Logo"
                        className="logo-babylon-login"
                      />
                      <span className="prd-title ml-3">Learnerscafe</span>
                    </h2>
                    <h4 className="font-weight-normal text-center">
                      User Login
                    </h4>
                  </div>

                  <form className="" onSubmit={handleSubmit(onSubmit)}>
                    <div className="mb-3">
                      <label>User Email</label>
                      <input
                        type="email"
                        className="form-control"
                        name="email"
                        defaultValue={
                          localStorage.getItem("email")
                            ? localStorage.getItem("email")
                            : ""
                        }
                        placeholder="Enter Your Email Address"
                        ref={register({ required: true })}
                      />
                      {errors.email && (
                        <ErrorMsg>* This Field is Required</ErrorMsg>
                      )}
                    </div>
                    <div className="mb-3">
                      <label>Password</label>
                      <input
                        id="password-field1"
                        type={passwordShown ? "text" : "password"}
                        className="form-control"
                        name="password"
                        defaultValue={
                          localStorage.getItem("password")
                            ? localStorage.getItem("password")
                            : ""
                        }
                        placeholder="Enter Your Password"
                        ref={register({ required: true })}
                      />
                      <span
                        onClick={togglePasswordVisiblity}
                        toggle="#password-field1"
                        className={`fa fa-fw ${
                          passwordShown ? "fa-eye-slash" : "fa-eye"
                        } field-icon toggle-password`}
                      />
                      {errors.password && (
                        <ErrorMsg>* This Field is Required</ErrorMsg>
                      )}
                    </div>

                    <div className="form-check">
                      <div className="float-left">
                        <input
                          type="checkbox"
                          className="form-check-input check-input"
                          id="exampleCheck1"
                          checked={isRememberMe}
                          onChange={() => setIsRememberMe(!isRememberMe)}
                        />
                        <label
                          className="form-check-label"
                          htmlFor="exampleCheck1"
                        >
                          Remember Me
                        </label>
                      </div>
                      <p className="mb-1">
                        <Link
                          to="/forget-password"
                          className="float-right small mt-1"
                        >
                          Forgot Password?
                        </Link>
                      </p>
                    </div>

                    <div className="input-group mb-5">
                      <button
                        className="btn bg-color shadow text-white float-left mt-4 pr-5 pl-5"
                        type="submit"
                      >
                        Login
                      </button>
                    </div>
                  </form>
                  <Footer />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </React.Fragment>
  );
}
