import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { toast } from "react-toastify";
import DatatableParams from "./DatatableParams";
export default function PackageDetails() {
  //GET ALL PACKAGE NAME -START
  const [packageDetail, setPackageDetail] = useState({});
  const { id } = useParams();
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { package_id: id },
      };

      const pkgDetailApiResp = await trackPromise(axios(config));
      // console.log("pkgDetailApiResp - " + pkgDetailApiResp.data.msg);
      setPackageDetail(pkgDetailApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END
  //

  let history = useHistory();
  //package delete
  async function deletePackage(packageId, packageType) {
    if (packageType === "custom") {
      toast("First Delete Institute. Then Delete Custom Package", {
        className: "toast-error",
      });
    } else {
      // console.log("package id: " + packageId);
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "delete",
        url: `/package-delete/${packageId}`,
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
      };
      const deletePkgApiResp = await trackPromise(axios(config));
      //console.log("deletePkgApiResp - " + deletePkgApiResp.data);
      if (deletePkgApiResp.data.status === "ok") {
        toast("Package Deleted Successfully", {
          className: "toast-error",
        });
        history.push("/package-list-view");
      } else {
        toast(deletePkgApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Package Detail
                      </h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link to="/package-list-view">
                        <span
                          className="shadow float-right badge badge-pill badge-bg"
                          style={{ padding: "10px 24px" }}
                        >
                          <i className="far fa-eye pr-2"></i>View All Package
                        </span>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    {/* /.card-header */}
                    <div className="card-body">
                      <div className="tab-content">
                        <div className="active tab-pane" id="activity">
                          {/* Post */}
                          <div className="post">
                            <table className="table table-borderless table-responsive">
                              <tbody>
                                <tr>
                                  <td>Title</td>
                                  <td>{packageDetail.title}</td>
                                </tr>
                                <tr>
                                  <td>Description</td>
                                  <td>{packageDetail.description}</td>
                                </tr>
                                <tr>
                                  <td>Display</td>
                                  <td>{packageDetail.display}</td>
                                </tr>
                                <tr className="bottom-border">
                                  <td>Package Type</td>
                                  <td>{packageDetail.type}</td>
                                </tr>
                              </tbody>
                            </table>
                            {packageDetail.params ? (
                              <DatatableParams data={packageDetail.params} />
                            ) : null}

                            <div className="text-center">
                              <button
                                className="btn btn-update mt-2 mb-2"
                                onClick={() =>
                                  deletePackage(
                                    packageDetail.id,
                                    packageDetail.type
                                  )
                                }
                              >
                                Delete
                              </button>
                            </div>
                          </div>
                          <LoadingIndicator />
                        </div>
                      </div>
                      {/* /.tab-content */}
                    </div>
                    {/* /.card-body */}

                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
